import React ,{useState, useEffect} from 'react';
import styles from '../style/login.module.css'
import logo from "../media/static/Logomain.JPG";
import { Link, Redirect } from 'react-router-dom';
import { Alert, AlertTitle } from '@material-ui/lab';
import axios from 'axios';
import Footer from '../Landing/Footer'
import { serverip } from '../Landing/constants'

function Signup(props) {
    const [taken, setTaken] = useState(false)
    const [matchAlert, setMatchAlert] = useState(false)
    const [passMatch  ,setPassMatch] = useState(false)
    const [mailError, setMailError] = useState(false)
    const [User_type, setUser_type] = useState("")
    const [userlist, setUserlist] = useState([])
    const [cred, setCred] = useState({
        nameUser: "",
        firstName: "",
        lastName: "",
        emailId: "",
        pass: "",
        confirmPassword: "",
    });
    const handle_usertype = (e) => {
        setUser_type(e.target.value)
        }
    useEffect(() => {
        const user_taken =async () => {
            await axios.get(`${serverip}/api/usersearch/`).then(res => {
                setUserlist(res.data)
            }
                )
        }
        user_taken()
       
    }, [])
    const usernametaken = () => {
        setTimeout(() => {
            userlist.map(user => {
                if (user.username === cred.nameUser) {
                    setTaken(true)
                }
                return null
            })
        }, 1000);
    }
   
    
    
    const handle_change = (e) => {
        setCred({ ...cred, [e.target.name]: e.target.value })
        
    }
    const checkmail = (e) => {
        var re = /\S+@\S+\.\S+/;
        if ((!re.test(e.target.value)) && (e.target.value !== '')) {
            setMailError(true)
        }
    }
    const checkpass = () => {
        if (cred.pass !== cred.confirmPassword)
            setPassMatch(true)
    }
        // const { name, value } = e.target;
        // switch (name) {
        //     case 'nameUser':
        //         errors.nameUser =
        //             value.length < 5
        //                 ? 'Full Name must be 5 characters long!'
        //                 : '';
        //         break;
        //     case 'pass':
        //         errors.pass =
        //             value.length < 8
        //                 ? 'Password must be 8 characters long!'
        //                 : '';
        //         break;
        //     default:
        //         break;
        // }

        // seterrors(errors);
  
    const handle_signup = async (e) => {
            if ((User_type !== "")&& (cred.firstName !=="")&& (cred.lastName !=="") &&(!mailError) && (!passMatch) &&(!taken)){
                await fetch(`${serverip}/api/users/`, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        "username": cred.nameUser,
                        "first_name": cred.firstName,
                        "last_name": cred.lastName,
                        "email": cred.emailId,
                        "password": cred.pass,
                        "User_type": User_type
                    }),
                })
                    .then(res => res.json())
                    .then(json => {
                        localStorage.setItem('token', json.token);
                        setCred({
                            userid: json.id,
                            nameUser: json.username,
                            firstName: json.first_name,
                            lastName: json.last_name,
                            emailId: json.email
                        })
                        fetch(`http://127.0.0.1:8000/api/usertype/${json.username}/`, {
                            method: 'PATCH',
                            headers: {
                                'Content-Type': 'application/json',
                                Authorization: `JWT ${localStorage.getItem('token')}`
                            },
                    
                            body: JSON.stringify({
                                "user_type": User_type,
                            }),
                        })
                 
                    })
            }
            else alert("Enter Valid Credential")
        
    }
if (localStorage.getItem('token') && (localStorage.getItem('token') !== 'undefined')) {

    if (User_type === "Patient"){
            return <Redirect to={{
                pathname: `profile/Patient/${cred.nameUser}`,
                state: {username :cred.nameUser}
            }}
    />;
    }
    else if (User_type === "Doctor") {
            return <Redirect to={{
                pathname: `/profile/Doctor/${cred.nameUser}`,
                state: {username :cred.nameUser}
            }} />;
    }
    else if (User_type === "Patholab") {
            return <Redirect to={{
                pathname: `/profile/Patholab/${cred.nameUser}`,
                state: {username :cred.nameUser}
            }} />;
    }
    else if (User_type === "Hospital") {
            return <Redirect to={{
                pathname: `/profile/Hospital/${cred.nameUser}`,
                state: {username :cred.nameUser}
            }} />;
        }
    else if (User_type === "Ambulance") {
            return <Redirect to={{
                pathname: `/profile/Ambulance/${cred.nameUser}`,
                state: {username :cred.nameUser}
            }} />;
        }
    else if (User_type === "Collector") {
            return <Redirect to={{
                pathname: `/profile/Collector/${cred.nameUser}`,
                state: {username :cred.nameUser}
            }} />;
        }
}
        
                    
        return (
            <React.Fragment>
                {taken ? 
          (<Alert onClose={() => {setTaken(false)}} severity="error">
            <AlertTitle>Username Already Taken</AlertTitle>
         <strong>Try another one</strong>
          </Alert>):null}
                {matchAlert ? 
          (<Alert onClose={() => {setMatchAlert(false)}} severity="error">
         <strong>Password did not match</strong>
          </Alert>):null}
                <section className={styles.section}>
                    <div className={styles.imgBx} style={{'background':'linear-gradient(45deg, #000, transparent'}}>
                        <img src={logo} alt="Sign Up on DocPlus" />
                        <Footer></Footer>
                    </div>
                    <div className={styles.contentBx}>
                        <div className={styles.formBx}>
                            <h2>Sign Up</h2>
                            <div className={styles.inputBx}>
                      <h1 className={styles.nameLabel}>User Type</h1>
                        <select id={styles.user_type} name="User_type" onChange={handle_usertype} required>
                          <option disabled="disabled"  selected>Choose any One</option>
                          <option value="Patient">User</option>
                          <option value="Doctor">Doctor</option>
                          <option value="Service">PSP</option>
                          <option value="Hospital">Hospital</option>
                          <option value="Ambulance">Ambulance</option>
                          <option value="Patholab">Patholab and Diagnostic Centre</option>
                          <option value="Collector">Employee</option>
                          </select>
                                  </div>
                            <form method="POST">
                    
                                <div className={styles.inputBx}>
                                    <label htmlFor="id_username">Username:</label><span><input type="text"  name="nameUser" onBlur={usernametaken} onChange={handle_change}  maxLength="150" autoFocus required id="id_username" /></span>
                    
                                </div>
                   
                                <div className={styles.inputBx}>
                                    <label htmlFor="id_first_name">First Name:</label><span><input type="text"  name="firstName" onChange={handle_change} className={styles.inputBx} placeholder="First Name"  maxLength="30" id="id_first_name" /></span>
                                </div>
                   
                                <div className={styles.inputBx}>
                                    <label htmlFor="id_last_name">Last Name:</label><span><input type="text" name="lastName" className={styles.inputBx} onChange={handle_change} placeholder="Last Name" maxLength="30" id="id_last_name" /></span>
                                </div>
                   
                                <div className={styles.inputBx}>
                                    <label htmlFor="id_email">Email:</label><span><input type="text" onBlur={checkmail} name="emailId" className={styles.inputBx} onChange={handle_change} placeholder="Email(if available)" maxLength="254" id="id_email" /></span>
                                    {mailError ? (<Alert onClose={() => {setMailError(false)}} severity="error">
                                    <strong>Enter Valid Email</strong>
                                    </Alert>):null}
                                </div>
                   
                                <div className={styles.inputBx}>
                                    <label htmlFor="id_password1">Password:</label><span><input type="password" name="pass" autoComplete="new-password" onChange={handle_change} required id="id_password1" /></span>
                                </div>
                   
                                <div className={styles.inputBx}>
                                    <label htmlFor="id_password2">Password confirmation:</label><span><input type="text" name="confirmPassword" onBlur={checkpass} onChange={handle_change} autoComplete="new-password" required id="id_password2" /></span>
                                    {passMatch ? (<Alert onClose={() => {setPassMatch(false)}} severity="error">
                                    <strong>Password did not match</strong>
                                    </Alert>):null}
                                </div>
                   
                                <div className={styles.inputBx}>
                                    <input type="button" onClick={handle_signup} value="Sign Up" />
                                </div>
                                <div className={styles.inputBx}>
                                    <p>Already have an account? <Link exact to="/Login">Log in</Link> </p>
                                </div>
                            </form>
                            
                            
                            {/* <h3>signup with Social media</h3>
                            <ul className={styles.sci}>
                                <li><img src={fb} width="30px" height="30px" alt="Facebook" /></li>
                                <li><img src={gmail} width="30px" height="30px" alt="Google" /></li>
                                <li><img src={twitter} width="30px" height="30px" alt="twitter" /></li>
                            </ul> */}
                        </div>
                    </div>
                </section>
               
            </React.Fragment>
        )
}


export default Signup
