import React, { useState, useEffect }from 'react'
import styles from'../style/profile.module.css'
import $ from 'jquery';
import JSONDATA from '../media/static/jsonFiles/statecity.json';
import axios from 'axios';
import profile from '../media/static/profile.png';
import { Redirect, useHistory } from 'react-router';
import PublishIcon from '@material-ui/icons/Publish';
import Navtop from '../Landing/Navtop';
import { TextField, Button , CircularProgress} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { Container} from "@material-ui/core";
import { Alert, AlertTitle } from '@material-ui/lab'
import Footer from '../Landing/Footer';
import { Link } from "react-router-dom";
import { serverip } from '../Landing/constants'

function Info(props) {
    const [state, setState] = useState("");
    const [profilePic, setProfilePic] = useState({ profile });
    const [patdata, setPatdata] = useState([])
    const [uData, setUData] = useState({email: '', first_name:'', last_name:'', username: '', id:''});
    const [post, setPost] = useState(false)
  const [username, setUsername] = useState(props.match.params.username)
  const [loading, setLoading] = useState(true)
  
    const [fdata , setfdata] = useState({
        user: '',
        name: '',
        dob: '',
        phone: '',
        address: '',
        zip_code: '',
        city:''
       
            })
            
    var history = useHistory()
    const handleInput = (e) => {
        setfdata({ ...fdata, [e.target.name]: e.target.value })
    }

    const handleImg = (e) => {
        setProfilePic(e.target.files[0]);
    }
    

    const dateChange = () => {
        $('#date').val($('#date').val().replace(/(....).(..).(..)/, "$1-$2-$3"));
    }
    useEffect(async () => {
        
        await axios.get(`${serverip}/api/detail/Patient/${username}`, {
            headers: {
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setPatdata(res.data)
          setState(res.data.state)
          getuser();
          setLoading(false)
            
        }).catch(err => {
            const error = (JSON.stringify(err.message))
            if (error.includes("status code 404"))
                setPost(true)
                getuser();
             })
    }, [])

            var formdata = new FormData();
            formdata.append('name', fdata.name);
            formdata.append('dob', fdata.dob);
            formdata.append('phone', fdata.phone);
            formdata.append('state', state);
            formdata.append('city', fdata.city);
            formdata.append('address', fdata.address);
            formdata.append('zip_code', fdata.zip_code);
            formdata.append('dob', fdata.dob);
    if (post) formdata.append('user', uData.id);
    
    var img_data = new FormData()
    img_data.append('profileImg', profilePic);
            let url = `${serverip}/api/Patient/`;
        const postData = async ()=>{
            await axios.post(url, formdata, {
                headers: {
                    'content-type': 'multipart/form-data',
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
            })
                .then(res => {
                    $('#btn').text('Submitted')
                    return <Redirect to="/" />;
                }).catch('Try again or Login again')
        }
    const putData = async () => {
        await axios.patch(`${serverip}/api/detail/Patient/${patdata.slug}/`, formdata, {
            headers: {
                'Content-Type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
                   }
        }).then(res => {
            $('#subBtn').text('Submitted')
            setTimeout(() => {
                $('#subBtn').text('Submit')
            }, 2500);
        })
    }
    const upload =async () => {
        await axios.patch(`${serverip}/api/detail/Patient/${patdata.slug}/`,img_data ,{
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            },
           
        }).then(alert("Uploaded Successfully"))
    }
            
    const getuser = async () => {
        await axios.get(`${serverip}/currentuser/`, {
              headers: {
                Authorization: `JWT ${localStorage.getItem('token')}`
              }
        }).then(resp => {
            setUData(resp.data)
          }).catch(err => {
              var error = (JSON.stringify(err.message))
              if (error.includes("status code 401"))
                  history.push('/login')
                
        })
              
    }
    if ((!loading) && (uData.username === username))
    return (
      <React.Fragment>
        <Navtop user={uData.username} user_type="Patient"></Navtop>
        <div className={styles.main}>
          <Container className={styles.container} maxWidth="lg">
            <div className={styles.top}>
              <div className={styles.imgBox}>
                <img  src={patdata.profileImg !== null ? patdata.profileImg : profile}></img>
              </div>
              <span className={styles.nameplate}>
                <h1>{patdata.name}</h1>
              </span>
            </div>
          </Container>

          <form method="POST" encType="multipart/form-data">
            <div className={styles.grid}>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Name</h2>
                <input
                  className={styles.userinp}
                  type="text"
                  onChange={handleInput}
                  placeholder={patdata.name}
                  id="name"
                  name="name"
                  required
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Phone No.</h2>
                <input
                  className={styles.userinp}
                  placeholder={patdata.phone}
                  type="number"
                  name="phone"
                  onChange={handleInput}
                  maxLength="10"
                  required
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Date of Birth</h2>
                <input
                  className={styles.userinp}
                  type="date"
                  onChange={(dateChange, handleInput)}
                  name="dob"
                  required
                />
                <span>{patdata.dob}</span>
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Profile Pic</h2>
                <input
                  type="file"
                  id="fileImg"
                  name="profileImg"
                  accept="image/*"
                  onChange={handleImg}
                />
                <Button
                  onClick={upload}
                  variant="contained"
                  id="uploadBtn"
                  color="secondary"
                >
                  {" "}
                  Upload{" "}
                </Button>
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>State</h2>
                <Autocomplete
                  value={state}
                  placeholder={state}
                  onChange={(event, value) => setState(value)}
                  freeSolo
                  options={Object.keys(JSONDATA).map((result) => result)}
                  renderInput={(params) => (
                    <TextField
                      className={styles.userinp}
                      {...params}
                      value={state}
                      variant="outlined"
                      name="state"
                      size="small"
                      placeholder={state}
                      style={{
                        width: "100%",
                        background: "#fff",
                        padding: "1px 2px",
                      }}
                    />
                  )}
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>City</h2>
                <input
                  className={styles.userinp}
                  placeholder={patdata.city}
                  type="text"
                  id={styles.city}
                  required
                  name="city"
                  onChange={handleInput}
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className="">Local Area</h2>
                <textarea
                  name="address"
                  placeholder={patdata.address}
                  style={{ width: "100%" }}
                  onChange={handleInput}
                  cols="30"
                  rows="3"
                  required
                ></textarea>
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>ZIP code</h2>
                <input
                  className={styles.userinp}
                  type="number"
                  placeholder={patdata.zip_code}
                  onChange={handleInput}
                  name="zip_code"
                  required
                />
              </div>
              <Button
                id="subBtn"
                variant="contained"
                color="primary"
                endIcon={<PublishIcon></PublishIcon>}
                onClick={post ? postData : putData}
              >
                Send
              </Button>
             
            </div>
          </form>
            </div>
            <Footer></Footer>
      </React.Fragment>
  )
  else
  return(
    <>
    <Alert onClose={() => {setLoading(false)}} severity="error">
          <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
        </Alert>
    <CircularProgress disableShrink />
      <h1>Authenticating Now...</h1><br/>
      <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
      <Link  to='/'>Home</Link>
      <footer>
        <Footer></Footer>
        </footer>
    </>
  )
}

export default Info
