import { Avatar, Paper, CircularProgress, Button } from '@material-ui/core'
import React, {useState, useEffect} from 'react'
import styles from '../style/faq.module.css'
import axios from 'axios'
import profile from '../media/static/profile.png';
import $ from 'jquery'
import Navtop from './Navtop';
import { useHistory } from 'react-router-dom';
import Faqlist from './Faqlist';
import { Alert, AlertTitle } from '@material-ui/lab';
import { Link } from 'react-router-dom';
import Footer from '../Landing/Footer';
import { serverip } from './constants'

function Faq(props) {
    const [blog, setBlog] = useState([])
    const [logged_in, setLogged_in] = useState(localStorage.getItem('token') ? true : false)
    const [usertype, setUsertype] = useState("")
    const [answer, setAnswer] = useState({ Name: "", desc: "", Answer: "" })
    const [bId, setBId] = useState(props.match.params.id)
    const [answers, setAnswers] = useState([])
    const [loading, setLoading] = useState(true)
    var history = useHistory();
    useEffect(() => {
        const getFaq = async () => {
            await axios.get(`${serverip}/api/detail/faq/${bId}/`, {
                headers: {
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
            }).then(res => {
                setBlog(res.data)
                setAnswers(res.data.Answer)
                gettype();
                setLoading(false)
            })
        }
        getFaq()
  }, [bId])
     const gettype = async() => {
        await axios.get(`${serverip}/api/usertype/${localStorage.getItem('name')}`, {
           headers: {
             Authorization: `JWT ${localStorage.getItem('token')}`
           }
        }).then(res => {
            if (localStorage.getItem('name') === res.data.user_name)
                setUsertype(res.data)
            else {
                alert('Username Altered')
                localStorage.removeItem('token')
                localStorage.removeItem('name')
                history.push('/login')
            }
         })
     }
        const answerjson = (e) => {
        setAnswer({ ...answer, [e.target.name]: e.target.value })
    }
    const addanswer = async () => {
        if ((blog.Answer)!==null) {
            var datajson = JSON.stringify({
                "Answer": [...(blog.Answer), { Name: usertype.profile_name, desc: answer.desc, Answer: answer.Answer }]
            })
        }
         else {
            datajson = JSON.stringify({
                "Answer": [{ Name: usertype.profile_name, desc: answer.desc, Answer: answer.Answer }]
            })
            }
        await axios.patch(`${serverip}/api/detail/faq/${bId}/`, datajson, {
            headers: {
                'content-type': 'application/json',
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            $('#resbtn').text('Submitted')
            window.location.reload();
        })
    }
    if (!loading)
    return (
        <>
            <Navtop user={usertype.user_name} user_type={usertype.user_type}></Navtop>
        <div className={styles.faqcontainer}>
            <div className={styles.questionpanel}>
                <p>{ blog.question}</p>
            </div>
            <div className={styles.anspanel}>
                <Paper>
                    <div className={styles.headpanel}><Avatar src={profile}></Avatar><h1>{blog.byfullname}</h1></div ><div className={styles.spantext}><span>{ blog.date}</span></div>
                    <div className={styles.anspart}>
                        <ul>
                            {(answers !== null)?
                                answers.map((answer,index) => {
                                    return (
                                        <li key={index}><p>{answer.Answer}</p><br /><span>{answer.Name}, &nbsp; { answer.desc }</span></li>
                                    )
                                }):<p>No one has responded on this</p>
                            }
                        </ul>
                    </div>
                </Paper>
                {(logged_in) && (usertype.user_type==="Doctor") &&
                        <div className={styles.ansinput}>
                        <label htmlFor="name">Name</label>
                        <input type="text" value={usertype.profile_name} onChange={()=>alert("Can not be Changed")}></input>
                        <label htmlFor="name">Degree and Designation</label>
                        <input type="text" name="desc" onChange={answerjson} placeholder="Degree ,Designation" required></input>
                        <label htmlFor="name">Add a Response to Topic </label>
                        <input type="text" name="Answer" row="3" column="20" onChange={answerjson} />
                        <button className={styles.resbtn} id="resbtn" onClick={addanswer}>Add Response</button>
                    </div>
                }
            </div>

            </div>
            <div style={{"position": "relative" ,"top":" 1000px"}}>
                <Faqlist user={usertype.userprofile}></Faqlist>
                </div>
            </>
    )
    else
    return(
      <>
      <Alert onClose={() => {setLoading(false)}} severity="error">
            <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
          </Alert>
      <CircularProgress disableShrink />
        <h1>Authenticating Now...</h1><br/>
        <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
        <Link  to='/'>Home</Link>
        <footer>
          <Footer></Footer>
          </footer>
      </>
    )
}


export default Faq
