import React, { useState } from "react";
import styles from "../style/footer.module.css";
import { Link } from "react-router-dom";
import fb from "../media/static/facebook.png";
import gmail from "../media/static/gmail.png";
import twitter from "../media/static/twitter.png";
import insta from "../media/static/insta.png";
import DeveloperProfile from "../media/static/Developer.jpg";
import $ from "jquery";
import axios from "axios";
import { Alert, AlertTitle } from "@material-ui/lab";
import card1 from "../media/static/ambulance.jpg";
import card2 from "../media/static/cardDoctor.jpg";
import card3 from "../media/static/cardpatho.jpg";
import RowCard from "./RowCard";
import logo from '../media/static/logo512.png'
import { serverip } from './constants'

function Footer() {
  const [formdta, setFormdta] = useState({});
  const [thkalert, setThkalert] = useState(false);
  const handleChange = (e) => {
    setFormdta({ ...formdta, [e.target.name]: e.target.value });
  };
  const submitcontact = async (e) => {
    e.preventDefault();
    var datajson = JSON.stringify({
      name: formdta.name,
      email: formdta.email,
      msgtype: formdta.msgtype,
      message: formdta.message,
    });

    await axios
      .post(`${serverip}/api/contact/`, datajson, {
        headers: {
          "content-type": "application/json",
        },
      })
      .then((res) => {
        $("#subbtn").text("Done");
        setThkalert(true);
        setTimeout(() => {
          setThkalert(false);
          $("#subbtn").text("Submit");
        }, 2500);
      });
  };
  return (
    <div className={styles.footerbox}>
      <footer className={styles.footer}>
        <div className={styles.footer_left}>
          <div style={{display:'flex', 'width':'min-content'}}>
        <img src={logo} height='50px' width='50px'></img>
            <h5 style={{ 'color': '#rgb(109 239 10)', 'whiteSpace':'nowrap' }}>HOMO SAVIOUR - Live For Humanity</h5>
            </div>
          {/* <img src={logo} width="30px" height="30px" /> */}
          <p>
             Homo Saviour-Live for Humanity is an Online Platform with
            the aim to help people in availing the facilities in Health,
            Agriculture and Education sector. Its main aim to reach out to the
            maximum number of people for their convenience in availing the
            facilities provided by the government as well as by the private
            organistaion. This is a platform for the people and by the
            people. Objective of this unit is to create an infrastructure where people from
            every class would be able to get a better health related services.
            Your contribution matters a lot .
            <Link to="/signup" style={{ color: "#00ffd0" }}>
              Join Us Now
            </Link>
          </p>
          <div className={styles.socialmedia}>
            <li>
              <img src={fb} width="20px" height="20px" alt="Facebook" />
            </li>
            <li>
              <img src={gmail} width="20px" height="20px" alt="Google" />
            </li>
            <li>
              <img src={twitter} width="20px" height="20px" alt="twitter" />
            </li>
            <li>
              <img src={insta} width="20px" height="20px" alt="Instagram" />
            </li>
          </div>
        </div>
        <ul className={styles.footer_right}>
          <li>
            <h2>Links</h2>
            <ul className={styles.box}>
              <li>
                <a  data-bs-toggle="modal"
                  id="aboutBtn"
                  data-bs-target="#ourwork">
                  About
                </a>
              </li>
              <li>
                <a
                  data-bs-toggle="modal"
                  id="proBtn"
                  data-bs-target="#developer"
                >
                  Owner Profile
                </a>
              </li>
              <li>
                <a
                  data-bs-toggle="modal"
                  id="fprmBtn"
                  data-bs-target="#contactform"
                >
                  Contact Us
                </a>
              </li>
              <li>
                <a
                  data-bs-toggle="modal"
                  id="bugBtn"
                  data-bs-target="#contactform"
                >
                  Report Bug
                </a>
              </li>
              {(localStorage.getItem('token')===null) && <li>
                <Link to="/signup">Join Us</Link>
              </li>}
            </ul>
          </li>
          <li>
            <h2>Important</h2>
            <ul className={styles.box}>
              <li>
                <a
                  data-bs-toggle="modal"
                  id="modalBtn"
                  data-bs-target="#TncDoctor"
                >
                  Terms and Condition
                </a>
              </li>
              <li>
                <a
                  data-bs-toggle="modal"
                  id="modalBtn"
                  data-bs-target="#services"
                >
                  Services
                </a>
              </li>
              <li>
                <a
                  data-bs-toggle="modal"
                  id="modalBtn"
                  data-bs-target="#monetary"
                >
                  Monetary Terms
                </a>
              </li>
            </ul>
          </li>
          <li>
            <h2>Official Address</h2>
            <ul className={styles.box}>
              <li>
                <Link to="#">H No.12</Link>
              </li>
              <li>
                <Link to="#">Madhusudanpur</Link>
              </li>
              <li>
                <Link to="#">Bhagalpur</Link>
              </li>
              <li>
                <Link to="#">812005</Link>
              </li>
              <li>
                <Link to="#">Bihar</Link>
              </li>
            </ul>
          </li>
        </ul>
        <div className={styles.footer_bottom}>
          <p>All Right reserved by &copy;Homo Saviour 2021</p>
        </div>
      </footer>

      <div
        className="modal fade"
        id="TncDoctor"
        tabIndex="-1"
        aria-labelledby="tncDoctorLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div
            className="modal-content"
            style={{ background: "linear-gradient(45deg, black, transparent)" }}
          >
            <div className="modal-header" style={{ background: "#34f9ff" }}>
            <img src={logo} height='50px' width='50px'></img>
              <h5 className="modal-title" id="tncDoctorLabel">
                Homo Saviour-Live For Humanity
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body" style={{ color: "white" }}>
              <h2>Terms and Condition</h2>
              <ul>
                <li>
                  Do not Cancel appointment if availability is marked true
                </li>
                <li>Online Consultaion should be done as soon as possible</li>
                <li>
                  Patient can consult or make an appointment for free upto 15
                  days from the last date of paying appointment Fees
                </li>
                <li>
                  No One is allowed to delete patient user from consult list
                  till the consult period of 15 days is over
                </li>
                <li>
                  if a patient could not reach on time, it's Payment receiver's
                  wish to cancel appointment{" "}
                </li>
                <li>
                  25% of amount shall be refunded in case of scheduled
                  appointment timing failure
                </li>
                <li>
                  In any other case of cancellation, except scheduled timing
                  failure, 100% amount shall be refunded to the payee
                </li>
                <li>
                  Request for refund shall be made as soon as the appointment is
                  cancelled by Payment receiver's side
                </li>
                <li>
                  Once the request is made for refund, Doctors will not be held
                  responsible for delay in refund
                </li>
                <li>
                  Unmark the availability from the sidebar of homepage as soon
                  as your desired number appointment list is full
                </li>
                <li>Settlement of the payment shall be done after on every sunday and Thursday</li>
                <li>
                  Account Details must be provided to the marchant just after
                  the registration
                </li>
                <li>
                  If a Patient wants to do cash payment, Settlement percentage
                  amount must be sent to HomoSaviour with in a week
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div
        className="modal fade"
        id="monetary"
        tabIndex="-1"
        aria-labelledby="monetaryLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div
            className="modal-content"
            style={{ background: "linear-gradient(45deg, black, transparent)" }}
          >
            <div className="modal-header" style={{ background: "#34f9ff" }}>
            <img src={logo} height='50px' width='50px'></img>
              <h5 className="modal-title" id="monetaryLabel">
                Homo Saviour-Live For Humanity
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body" style={{ color: "white" }}>
              <h2>Terms and Condition</h2>
              <ul>
                <li>
                 All Transactions are safe and secure by the Payment Service Provider
                </li>
                <li>All the working unit including Doctors, Hospitals and other units of Homo Saviour will have to share a small percentage of their total received amount with Homo-Saviour</li>
                <li>
                  Percentage of amount being charge is only for the Web services such as Server Cost and for advertisement Purpose
                </li>
                <li>
                  Each and every unit is equally responsible to promote Homo Saviour at their respective workplace 
                </li>
                <li>
                  Our main aim is to reach out to the maximum number people, so that we could sustain and provide services to the People
                </li>
                <li style={{'color':'blue'}}>
                 <b> Only 8 % of the the total Transaction done by any working unit ,excluding the refunded amount, will be charged as web services and advertisement cost for Homo Saviour</b>
                </li>
                <li>
                  Homo Saviour is not responsible for any kind of taxes that user pays to any of the government bodies
                </li>
                <li>
                  All working units are responsible to keep track of their transactions and other details necessary for them
                </li>
                <li>
                  All the details and data are completely secure with us and will only be provided to the user on their demand after authentication
                </li>
                <li>Settlement of the payment to all the working unit shall be done on every sunday and Thursday or otherwise depending upon the situation</li>
                <li>
                  Account Details must be provided to the marchant just after
                  the user receives their first amount
                </li>
                <li>
                  If a Patient wants to do cash payment, Settlement percentage
                  amount must be sent to HomoSaviour Accounts with in a week
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div
        className="modal fade"
        id="ourwork"
        tabIndex="-1"
        aria-labelledby="OurWorkLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div
            className="modal-content"
            style={{ background: "linear-gradient(45deg, black, transparent)" }}
          >
            <div className="modal-header" style={{ background: "#34f9ff" }}>
            <img src={logo} height='50px' width='50px'></img>
              <h5 className="modal-title" id="OurWorkLabel">
                Homo Saviour-Live For Humanity
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body" style={{ color: "white" }}>
              <h2>About Us and Our Work</h2>
              <ul>
                <li>
                  Homo Saviour-Live for Humanity is totally devoted towards the
                  movement 'Digital India'
                </li>
                <li>
                  To reach out to the maximum number of needy people and serve
                  them
                </li>
                <li>
                  We together with the Health Workers is devoted towards making
                  a Healthy India
                </li>
                <li>
                  Our maximum effort will be towards reaching out to the
                  technologically illiterate People and provide them with best
                  health service with much more ease and convenience
                </li>
                <li>
                  People who resides in the remote areas will have the facility
                  to have an appointment with the doctors or to avail Ambulance
                  and Diagnostic centre facilities without waiting outside the
                  clinic or hospital for a long time{" "}
                </li>
                <li>
                  Those who wants to avail Patholab facilities will have an
                  option to give their sample from home
                </li>
                <li>
                  A person will be assigned by the Lab to collect the sample
                </li>
                <li>
                  Reports will be sent to the whatsapp as soon as it is made
                  available
                </li>
                <li>
                  Hard Copy of the reports will be delivered to you by the
                  sample Collector if needed
                </li>
                <li>
                  Patient can make a booking for the hospital bed to ensure
                  quick diagnosis by the doctors
                </li>
                <li>
                  As soon as , available bed in the hospital is booked, Hospital
                  will make complete arrangements for quick diagnosis and
                  checkup
                </li>
                <li>
                  Patient will be able to know about the availability status of
                  bed and doctors in hospitals
                </li>
                <li>
                  Any Payment to the hospital Patholab and Diagnostic Centre is
                  non-refundable in any case except cancellation is made by
                  hospital ,Doctor, Patholab or Diagnostic Center itself
                </li>
                <li>
                  Refunds shall be made to the same account used for the payment
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div
        className="modal fade"
        id="developer"
        tabIndex="-1"
        aria-labelledby="developerLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div
            className="modal-content"
            style={{ background: "linear-gradient(45deg, black, transparent)" }}
          >
            <div className="modal-header" style={{ background: "#34f9ff" }}>
              <img src={logo} height='50px' width='50px'></img>
              <h5 className="modal-title" id="developerLabel">
                Homo Saviour-Live For Humanity
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body" style={{ width: "fit-content", 'padding':'0' , textAlign:'center' }}>
              <h2 style={{'color':'#fff'}}>Owner Profile</h2>
              <div className="card" style={{ width: "27rem" , "background": "linear-gradient(45deg, black, transparent)" , "color": "#fff" }}>
                <img
                  className="card-img-top"
                  src={DeveloperProfile}
                  style={{ width: "276px", height: "287px", margin: "auto" }}
                  alt="Card image cap"
                />
                <p style={{'fontSize':'12px'}}>Developed and Designed by</p>
                <div className="card-body">
                  <h3 className="card-title">ABHISHEK KUMAR JHA</h3>
                  <p className="card-text">Bhagalpur, Bihar</p>
                  <p className="card-text">B.Tech</p>
                </div>
                <ul className="list-group list-group-flush" >
                  <li className="list-group-item">A Humanitirian</li>
                  <li className="list-group-item">Young Visionary</li>
                  <li className="list-group-item">A beginner</li>
                </ul>
                <div className="card-body" >
                  <li>Building myself from every mistake I have ever made.</li>
                  <li>Let us come together and Serve the nation</li>
                  <li>Starting Unknown, Finishing Unforgettable</li>
                  <li style={{'fontSize':'10px'}}>Motivated By : ....</li>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        className="modal fade"
        id="contactform"
        tabIndex="-1"
        aria-labelledby="contactLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div
            className="modal-content"
            style={{ background: "linear-gradient(45deg, black, transparent)" }}
          >
            <div className="modal-header" style={{ background: "#34f9ff" }}>
            <img src={logo} height='50px' width='50px'></img>
              <h5 className="modal-title" id="contactLabel">
                Homo Saviour-Live For Humanity
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div
              className="modal-body"
              style={{ width: "fit-content", margin: "auto" }}
            >
              {thkalert ? (
                <Alert
                  onClose={() => {
                    setThkalert(false);
                  }}
                >
                  <AlertTitle>Thank You....</AlertTitle>
                </Alert>
              ) : null}
              <h2 style={{ height: "44px", fontSize: "28px", color: "#fff" }}>Contact Us</h2>
              <div className={styles.form}>
                <form>
                  <div className={styles.inpform}>
                    <h5>Select Type</h5>
                    <select name="msgtype" onChange={handleChange} required>
                      <option disabled value="none">
                        Choose any One
                      </option>
                      <option value="Complaint">Complaint</option>
                      <option value="Suggestion">Suggestion</option>
                      <option value="Bug">Report Bug</option>
                    </select>
                  </div>
                  <div className={styles.inpform}>
                    <h5>Name</h5>
                    <input
                      type="text"
                      name="name"
                      onChange={handleChange}
                      required
                    />
                  </div>
                  <div className={styles.inpform}>
                    <h5>Email</h5>
                    <input
                      className="form-control"
                      onChange={handleChange}
                      type="email"
                      name="email"
                    />
                  </div>
                  <div className={styles.inpform}>
                    <h5>Write here</h5>
                    <input
                      className="form-control"
                      onChange={handleChange}
                      style={{ height: "100px" }}
                      type="text"
                      name="message"
                      required
                    />
                  </div>
                  <button
                    id="subbtn"
                    onClick={submitcontact}
                    className="btn btn-success"
                  >
                    Submit
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        className="modal fade"
        id="services"
        tabIndex="-1"
        aria-labelledby="serviceLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div
            className="modal-content"
            style={{ background: "linear-gradient(45deg, black, transparent)" }}
          >
            <div className="modal-header" style={{ background: "#34f9ff" }}>
            <img src={logo} height='50px' width='50px'></img>
              <h5 className="modal-title" id="serviceLabel">
                Homo Saviour-Live For Humanity
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div
              className="modal-body"
              style={{ width: "fit-content", margin: "auto",  'padding':'0' }}
            >
              <div className="cards">
                <p style={{ color: "white", fontSize: "15px" }}>
                  Join Us to avail these facilities
                </p>
                <div className="cardBox">
                  <RowCard
                    title="Ambulance 24*7"
                    desc="the primary providers of a 24/7 response to medical and trauma related emergencies.  Medical staff available inside ambulances are well trained..."
                    imgCard={card1}
                  ></RowCard>
                </div>
                <div className="cardBox">
                  <RowCard
                    title="Doctors 24*7"
                    desc="All renowned doctors from all over INDIA at your fingertips for all related emergencies.  Well trained medical staff for essential  service..."
                    imgCard={card2}
                  ></RowCard>
                </div>
                <div className="cardBox">
                  <RowCard
                    title="Patholab and Diagnostic Centre 24*7"
                    desc="At Home service, Get your report from all  the leading test laboratory of India.  Sample pick up service available 24*7 with complete care and hygiene... "
                    imgCard={card3}
                  ></RowCard>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;
