import axios from "axios";
import React, { useState} from "react";

import { TextField } from "@material-ui/core";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import styles from "../style/rowcard.module.css";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { FormHelperText } from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import JSONDATA from '../media/static/jsonFiles/statecity.json';
import { Link } from "react-router-dom";
import profile from '../media/static/profile.png';
import { serverip } from './constants'


function SearchRow(props) {
  const [stateName, setstateName] = useState("");
  const [city, setCity] = useState("");
  const [list, setList] = useState([]);
  async function SearchDoctor() {
    if  (city !== "") {
      const requests = await axios.get(
        `${serverip}/api/Doctor/?search=${city}`
      );
      setList(requests.data);
      return requests;
    }
    else if ((city === "") && (stateName !== "")) {
      const requests = await axios.get(
        `${serverip}/api/Doctor/?search=${stateName}`
      );
      setList(requests.data);
      return requests;
    }
  }
  const sendRequest = (e) => {
  var rcvid= (e.target.id)
    var conf = window.confirm("Do you want to send request")
    if (conf)
      {
      if (props.myID !== rcvid) {
        axios.post(`${serverip}/api/send_request/${props.myID}/${rcvid}/`, {
          headers: {
            Authorization: `JWT ${localStorage.getItem('token')}`
          }
        }).then(res => {
        alert(res.data)
          
        }).catch(err => {
          alert("Could not send request, Try after sometime")
        })
      }
      else alert("You Should find someone else...")
  }
}

  return (
    <React.Fragment>
      
      <div className={styles.bg}>
        <div className={styles.top}>
      <div className={styles.input}>
    <Autocomplete
            freeSolo
            defaultValue={stateName}
        id="state"
        disableClearable
        options={Object.keys(JSONDATA).map((result) => result)}
        renderInput={(params) => (
          <TextField
            {...params}
            label="Search By State"
            margin="normal"
            variant="outlined"
            name="stateName"
            onChange={(e) => { setstateName(e.target.value) }}
            InputProps={{ ...params.InputProps, type: 'search' }}
          />
          )}
      />
          <Button  variant="contained" color="primary" onClick={SearchDoctor}>Search</Button>
        </div>
        <div className={styles.input}>
          <TextField
            label="City, Name or Local area"
            margin="normal"
            variant="outlined"
            name="city"
              onChange={(e) => { setCity(e.target.value) }} />
             <Button style={{margin : "16px"}} variant="contained" color="primary" onClick={SearchDoctor}>Search</Button>
        </div>
        </div>
      
      <h2 className={styles.heading}>DOCTORS IN {city}</h2>
      <div className={styles.row_doctors}>
          {(list.length)?
            list.map((doctor) => {
          return (
            <Card className={` ${styles.row_doctor}`} style={{ overflow: "visible"}}>
              <CardActionArea>
                
                <LazyLoadImage
                  component="img"
                  alt={doctor.name}
                  height="140"
                  style={{
                    height: "140",
                    backgroundPosition: "center",
                    backgroundRepeat: "no-repeat",
                    backgroundSize: "cover",
                  }}
                  key={doctor.id}
                  src={(doctor.profileImg)?doctor.profileImg:profile}
                  loading="lazy"
                  effect="blur"
                  title={doctor.name}
                  className={styles.img}
                />
                <CardContent style={{padding:'8px'}}>
                  <Typography
                    className={styles.title}
                    gutterBottom
                   
                  >
                    {doctor.name}
                    
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                    className={styles.desc}
                  >
                    {doctor.desc}
                    
                  </Typography>
                  <span className={styles.uni}>{ doctor.spcl}</span>
                </CardContent>
              </CardActionArea>
              <CardActions>
                <div className={styles.item}>
                <Link className={styles.btn} to={{
                            pathname: `/userprofile/${doctor.slug}`,
                            }} size="small" color="primary">Visit Profile
                </Link>
                  <button id={doctor.userdoc} onClick={sendRequest} style={{'background':'linear-gradient(45deg, violet, transparent)'}} >Online Consult:&#8377;{doctor.consult_fees}</button>
                </div>
                <div className={styles.item}>
                <Link className={styles.btn} to={{
                                     pathname: `/appointment/Doctor/${doctor.slug}`,
                            }} size="small" color="primary">
                    BOOK NOW
                </Link>
                <span className={styles.price}>&#8377;{doctor.apnt_fees}</span>
                </div>
              </CardActions>
            </Card>
          );
            }): (<h1>No Result Found, will be available soon </h1>)
      }
      </div>
      </div>
      </React.Fragment>
  );
}

export default SearchRow;
