import styles from '../style/sresult.module.css'
import React, { useState }from 'react'
import { Avatar} from '@material-ui/core'
import online from '../media/static/online.png';
import offline  from '../media/static/Offline1.png';
import {  useHistory } from "react-router";
import { Link } from "react-router-dom";
import axios from 'axios';
import $ from 'jquery';
import { Alert, AlertTitle } from '@material-ui/lab';
import { serverip } from './constants'
function SearchResult(props) {
    const [available, setAvailable] = useState(false)
    const [friend, setFriend] = useState(props.friendlist)
    const [alertshow, setAlertshow] = useState(false)

    if (props.availability) {
        setAvailable=true
    }
    var history = useHistory()
    const chatRedirect = (e) => {
        friend.forEach(element => {
            if (element === props.myId) 
                history.push(`/consult/${e.target.id}`)
            else setAlertshow(true) 
                 
        });
    }
    
    const sendRequest = (e) => {
        if (props.myId !== props.thisId) {
            axios.post(`${serverip}/api/send_request/${props.myId}/${props.thisId}/`, {
                headers: {
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
            }).then(res => {
                if (res.data === "Request Sent")
                    $(`#${e.target.id}`).text('sent')
                else $(`#${e.target.id}`).text('Already Sent')
      
            }).catch(err => {
                alert("Could not send request, Try after sometime")
            })
        }
        else alert("You Should find someone else...")
    }
    function contime(time) {
        var timeString = time;
        var H = + timeString.substr(0,2)
        var h = H % 12 || 12;
        var ampm = (H < 12 || H === 24) ? "AM" : "PM";
        timeString = h + timeString.substr(2, 3)  + " "+ ampm;
        return timeString
    }

    return (
        <>
             {alertshow ? 
          (<Alert  onClose={() => {setAlertshow(false)}} severity="error">
            <AlertTitle>Not in Patient List</AlertTitle>
         <strong>Send request and Make payment</strong>
          </Alert>):null}
        <div className={styles.container}>
           
            <div className={styles.top}>
                <Avatar className={styles.img} alt={props.name} src={props.proPic} />

                { props.avl ? (
                <img src={ online} height="10px" width="10px" alt="Online" style={{"position":"absolute"}}></img>):(<img src={ offline} height="10px" width="15px" alt="Unavailable" style={{"position":"absolute"}}></img>)}
                    <div className={styles.title_text} > <Link to={`/userprofile/${props.slug}`} style={{'whiteSpace':'pre-wrap', 'textDecoration':'none'}}>{props.name}</Link>
                        <div className={styles.spcl}>{props.desc}</div>
                    <div className={styles.time}><span>From: { (props.Time_from)?contime(props.Time_from):null }</span><br/>
                    <span>To: {(props.Time_to)?contime(props.Time_to):null}</span></div></div></div>
                    <div className={styles.right}>
                <span style={{ "borderRight": "1px solid", "padding": "3px", "borderRadius": "9px" }}>Consult Fees:<br/> &#8377; {props.consult_fees }</span><br/>
                        <span style={{"borderRight": "1px solid", "padding": "3px","borderRadius": "8px"}}>Clinic Fees:<br/> &#8377; { props.apnt_fees }</span>
                        <div className={styles.bottom}>
                            <button style={{'textDecoration':'none', 'fontSize':'11px'}} className={styles.btn} onClick={chatRedirect}
       size="small" id={props.slug}> Online Consult</button>
                            <Link style={{'textDecoration':'none','display':'block','background': 'linear-gradient(45deg, #ff00ee, white)', 'margin':'2px'}} className={styles.btn} to={{
                                     pathname: `/appointment/Doctor/${props.slug}`,
                            }}
       size="small" id={props.slug}> Appointment</Link>
                    <button style={{ 'textDecoration': 'none', 'display': 'block', 'margin': '2px', 'background': 'linear-gradient(45deg, #05fdfa, white)', 'fontSize': '14px' }} className={styles.btn} onClick={sendRequest}
                        size="small" id={`send${props.slug}`}>Send Request</button>
                </div>
                    </div>           
            </div>
            </>
    )
}

export default SearchResult