import React, { useState, useEffect, createContext} from 'react';
import styles from'../style/home.module.css'
import profile from '../media/static/profile.png';
import $ from 'jquery';
import Posters from './Posters';
import {  makeStyles} from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import PublishIcon from '@material-ui/icons/Publish';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import axios from 'axios';
import { Link, useHistory } from 'react-router-dom';
import ReplayIcon from '@material-ui/icons/Replay';
import { Alert, AlertTitle } from '@material-ui/lab';
import Clock from './Clock';
import Navtop from './Navtop';
import Chat from '../chat/Chat'
import { Button, CircularProgress } from '@material-ui/core';
import SearchResult from './SearchResult';
import Requestlist from './Requestlist';
import Footer from './Footer'
import style from '../style/footer.module.css'
import {serverip} from './constants'

function Home(props) {
  const [result, setResult] = useState([])
  const [details, setDetails] = useState({ email: '', first_name: '', last_name: '', username: '', id: '' })
  const [info, setInfo] = useState([])
  const [available, setAvailable] = useState()
  const [error, setError] = useState(false)
  const [loading, setLoading] = useState(true)
  const [searchInp, setSearchInp] = useState("")
  const [usertype, setUsertype] = useState(localStorage.getItem('type')?localStorage.getItem('type'):"")
  const [timedata, setTimedata] = useState({ Time_from: '', Time_to: '', availability: available })
  const [avldata, setAvldata] = useState({total_bes: '', available_bed: '',booking_price:'', availability: available })
  const [ambdata, setAmbdata] = useState({wic_charge: '', availability:available})
  const [sampledta, setSampledta] = useState()
  const [availabilityalert, setAvailabilityalert] = useState(false)
  const [nameuser, setNameuser] = useState(localStorage.getItem('name') ? localStorage.getItem('name') : "")
  

  
  var history = useHistory();
  useEffect(() => {
    axios.get(`${serverip}/currentuser/`, {
      headers: {
        Authorization: `JWT ${localStorage.getItem('token')}`
      }
    })
      .then(async (request) => {
        setDetails(request.data)
        if (request.data.username !== nameuser) {
          localStorage.removeItem('name')
          localStorage.setItem('name', request.data.username)
        }
        else
          setNameuser(request.data.username)
      })}, [])
    
  useEffect(async () => {
    await axios.get(`${serverip}/api/usertype/${nameuser}`, {
      headers: {
        Authorization: `JWT ${localStorage.getItem('token')}`
      }
    }).then(async (res) => {
      setUsertype(res.data.user_type)
      localStorage.setItem('type', res.data.user_type)
      await axios.get(`${serverip}/api/detail/${usertype}/${nameuser}`, {
        headers: {
          Authorization: `JWT ${localStorage.getItem('token')}`
        }
      }).then(req => {
        setLoading(false)
        setInfo(req.data)
        { (req.data.availability)?$('#checkavl').attr('checked', true ) : $('#checkavl').attr('checked', false) }
      })
     
    })
  }, [])
      
  

  const handleSearchInp = (e) => {
    setSearchInp(e.target.value)
  }
  const handlesmp = (e) => {
    setSampledta({ ...sampledta, [e.target.name]: e.target.value })
}
  
    const usestyles = makeStyles((theme) => ({
        root: {
          width: '100%',
        },
        heading: {
          fontSize: theme.typography.pxToRem(15),
          fontWeight: theme.typography.fontWeightRegular,
        },
      }));
      const classes = usestyles();
    
    const ToggleClass =async () => {
            $('.'+ styles.left_Side ).toggleClass(styles.active);
            $('.'+ styles.right_Side ).toggleClass(styles.active);
      $('.' + styles.toggle).toggleClass(styles.active);
  }
  const handle_logout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('name');
    localStorage.removeItem('type');
};

  const handledocSearch = async () => {
    if (!result.length){
      await axios.get(`${serverip}/api/Doctor/`, {
        headers: {
          Authorization: `JWT ${localStorage.getItem('token')}`
        }
      }).then(res => {
        setResult(res.data)
      }).catch(
        setError(true)
      )
  }
  }
  const handleinp = (e) => {
    setTimedata({...timedata, [e.target.name]: e.target.value})
    setAvldata({ ...avldata, [e.target.name]: e.target.value })
    setAmbdata({ ...ambdata, [e.target.name]: e.target.value })
  }
  var form_data = new FormData()
  const handleAvailability = (e) => {
    if (e.target.value) {
      setAvailabilityalert(true)
      setTimeout(() => {
        setAvailabilityalert(false)
      }, 5000);
      if (e.target.checked) {
        setAvailable(true)
      }
      else {
        setAvailable(false)
      }
    }
  }

  function contime(time) {
    var timeString = time;
    var H = + timeString.substr(0,2)
    var h = H % 12 || 12;
    var ampm = (H < 12 || H === 24) ? "AM" : "PM";
    timeString = h + timeString.substr(2, 3)  + " "+ ampm;
    return timeString
}
  const setTime = async () => {
    var form_data = new FormData()
    if ((timedata.Time_from === '') && (timedata.Time_to === '')) {
      form_data.append('availability', available);
    }
    else {
    form_data.append('Time_from', timedata.Time_from);
    form_data.append('Time_to', timedata.Time_to);
    }
    
    await axios.patch(`${serverip}/api/detail/Doctor/${details.username}/`, form_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            },
           
    }).then(res => {
      $('#subBtn').text("Done")
      setTimeout(() => {
        $('#subBtn').text("Set Time")
      }, 2000);
    })
  }
  const setAvl = async () => {
    var form_data = new FormData()
    if ((avldata.total_bes === '')) {
      form_data.append('available_bed', avldata.available_bed);
      form_data.append('booking_price', avldata.booking_price);
    }
    else if ((avldata.total_bes === '') && (avldata.available_bed === '') && (avldata.booking_price === ''))
      form_data.append('availability', available);
    else {
      form_data.append('total_bes', avldata.total_bes);
      form_data.append('available_bed', avldata.available_bed);
      form_data.append('booking_price', avldata.booking_price);
      form_data.append('availability', available);
    }
    await axios.patch(`${serverip}/api/detail/Hospital/${details.username}/`, form_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            },
           
        }).then($('#subBtn').text("Done"))
  }
  const setAmb = async () => {
    if(ambdata.wic_charge!=='')
      form_data.append('wic_charge', ambdata.wic_charge);
      form_data.append('availability', available);
    
    await axios.patch(`http://127.0.0.1:8000/api/detail/Ambulance/${details.username}/`, form_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            },
           
    }).then(res => {
      $('#subBtn').text("Done")
      setTimeout(() => {
        $('#subBtn').text("Update")
      }, 2000);
    })
     
  }
  const patch_sample = async () => {
    if (((info.sample_collected).length) && (sampledta!=null)) {
        var datajson = JSON.stringify({
          "sample_collected": [...(info.sample_collected), sampledta],
        })
    }
     else if(sampledta!=null){
        datajson = JSON.stringify({
          "sample_collected": [sampledta],
        })
        }
      else datajson = {'availability': available}
    await axios.patch(`http://127.0.0.1:8000/api/detail/Collector/${details.username}/`, datajson, {
        headers: {
            'content-type': 'application/json',
            Authorization: `JWT ${localStorage.getItem('token')}`
        }
    }).then(res => {
        $('#subBtn').text('Uploaded')
    })
}

  if(!loading)
    return (
      <>
        <div>
          <Navtop user={nameuser} user_type={usertype}></Navtop>
          {availabilityalert ? 
          (<Alert onClose={() => {setAvailabilityalert(false)}}>
            <AlertTitle>Wrong Credential</AlertTitle>
            Always check the checkbox more than once to set your availability<strong>For Confirmation</strong>
          </Alert>):null}
            <div id={styles.root1}>
        <div className={styles.left_Side}  id="left_Side">
            <div className={styles.profileText}>
                <div className={styles.imgBox}>
                    <img src={((info.profileImg)!==null)?info.profileImg:profile} alt={details.username} />
                </div>
              <h2>{ info.name}<br></br><span>{ info.city}</span></h2>
            </div>
                    <div className="contactInfo">
              <div className={classes.root}>
              
      <Accordion style={{margin: "10px" , padding : "5px 15px 10px"}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>ContactInfo</Typography>
        </AccordionSummary>
        <AccordionDetails>
                <ul>
                    <li>
                        <span className={styles.icon}><i className="fa fa-phone" aria-hidden="true"></i></span>
                        <span className="text">{ info.phone}</span>
                    </li>
                    <li>
                        <span className={styles.icon}><i className="fa fa-envelope" aria-hidden="true"></i></span>
                        <span className="text">{ details.email}</span>
                      </li>
                      {(info.address !== "") &&
                        (<li>
                          <span className={styles.icon}></span>
                          <span className="text">{info.address}</span>
                        </li>
                        )}
                    <li>
                        <span className={styles.icon}></span>
                        <span className="text">{ usertype}</span>
                    </li>
                </ul>
        </AccordionDetails>
                </Accordion>
             
                {(usertype==="Doctor")? 
                (<div className={styles.setavl}>
                    <div>
                      <span style={{'color':'#fff'}} >Time From</span><br/>
                      <input type="time" className={styles.time} name="Time_from" onChange={handleinp} /><br />
                      <p style={{'color':"#fff"}}>{(info.Time_from)?contime(info.Time_from):null} </p><br />
                      <span style={{'color':'#fff'}}>Time To</span><br/>
                      <input type="time" className={styles.time} name="Time_to" onChange={handleinp} /><br />
                      <p style={{'color':"#fff"}}>{(info.Time_to)?contime(info.Time_to):null }</p><br />  
                      <span style={{'color':'#fff'}}>Set Availability</span>
                      <input className={styles.time} id='checkavl' type="checkbox" onClick={handleAvailability} name="availability" /><br/>
                      <span style={{'color':'#fff', 'fontSize':'13px'}}>{(available)? "Present - " : "Absent -  " }</span>
                        <Button id="subBtn" variant="contained" color="primary" endIcon={<PublishIcon></PublishIcon>} onClick={setTime}>Set Time </Button><br /></div>
                      <div style={{'width':'100%'}}><Clock></Clock></div>
                  </div>):(usertype==="Hospital")? 
                (<div className={styles.setavl}>
                    <div>
                      <span style={{'color':'#fff'}} >Total Beds</span><br/>
                      <input type="text" className={styles.avlbed} name="total_bes" onChange={handleinp} /><br />
                      <p>{info.total_bes} </p><br />
                      <span style={{'color':'#fff'}}>Available Bed</span><br/>
                      <input type="text" className={styles.avlbed} name="available_bed" onChange={handleinp} /><br />
                      <p>{info.available_bed }</p><br />
                      <span style={{'color':'#fff'}}>Booking Charge</span><br/>
                      <input type="number" className={styles.avlbed} name="booking_price" onChange={handleinp} /><br />
                      <p>{info.booking_price }</p><br />
                      <span style={{'color':'#fff'}}>Set Availability</span>
                      <input className={styles.time} id='checkavl' type="checkbox" onClick={handleAvailability} name="availability" /><br/>
                      <span style={{'color':'#fff', 'fontSize':'13px'}}>{(available)? "Present - " : "Absent -  " }</span>
                          <Button id="subBtn" variant="contained" color="primary" endIcon={<PublishIcon></PublishIcon>} onClick={setAvl}>Update </Button><br /></div>
                      <div style={{'width':'100%'}}><Clock></Clock></div>
                  </div>):(usertype==="Collector")?(<div className={styles.setavl}>
                    <div>
                      <span style={{'color':'#fff'}} >Test Name</span><br/>
                      <input type="text" className={styles.avlbed} name="test" onChange={handlesmp} /><br />
                      
                      <span style={{'color':'#fff'}}>patient Name</span><br/>
                      <input type="text" className={styles.avlbed} name="patientName" onChange={handlesmp} /><br />
                    
                      
                      <span style={{'color':'#fff'}}>Amount Collected</span><br/>
                      <input type="number" className={styles.avlbed} name="amount" onChange={handlesmp} /><br />
                      <span style={{'color':'#fff'}}>Patient Username</span><br/>
                      <input type="text" placeholder="Enter as it is" className={styles.avlbed} name="userpatient" onChange={handlesmp} /><br />
                      
                      <span style={{'color':'#fff'}}>Set Availability</span>
                      <input className={styles.time} id='checkavl' type="checkbox" onClick={handleAvailability} name="availability" /><br/>
                      <span style={{'color':'#fff', 'fontSize':'13px'}}>{(available)? "Present - " : "Absent -  " }</span>
                          <Button id="subBtn" variant="contained" color="primary" endIcon={<PublishIcon></PublishIcon>} onClick={patch_sample}>Update </Button><br /></div>
                      <div style={{'width':'100%'}}><Clock></Clock></div>
                    </div>): (usertype==="Ambulance")? 
                (<div className={styles.setavl}>
                    <div>
                      <span style={{'color':'#fff'}} >Minimum Booking Charge</span><br/>
                      <input type="number" className={styles.avlbed} name="wic_charge"  onChange={handleinp} /><br />
                      <p>{info.wic_charge} </p><br />
                      <span style={{'color':'#fff'}}>Set Availability</span>
                              <input className={styles.time} id='checkavl' type="checkbox" onClick={handleAvailability} name="availability" /><br />
                              <span style={{'color':'#fff', 'fontSize':'13px'}}>{(available)? "Present - " : "Absent -  " }</span>
                      <Button id="subBtn" variant="contained" color="primary" endIcon={<PublishIcon></PublishIcon>} onClick={setAmb}>Update </Button><br /></div>
                      <div style={{'width':'100%'}}><Clock></Clock></div>
                  </div>):(<div style={{ 'width': '100%' }}><Clock></Clock></div>)
                }
                </div>
                <div className={styles.homefooter}>
                  <Footer></Footer>
                  </div>
            </div>

            </div>
        <div className={styles.right_Side} id="right_Side">
        <div className={styles.topbar}>
                <div className={styles.toggle} id="toggle" onClick={ToggleClass}></div>
        <div className={styles.search}>
          
            <label>
                  <input type="text" onClick={handledocSearch} onChange={handleSearchInp} placeholder="search for Doctors by name" /><i className="fa fa-search" id={styles.fa} aria-hidden="true"></i><i className="fa fa-times" onClick={()=>{setSearchInp("")}} id={styles.fatimes} aria-hidden="true"></i>
                  
                  <ul style={{"position": "absolute","zIndex": "5", 'height':'300px', 'overflowY':'auto', "margin": "5px"}}>
                    {
                      result.filter((doc) => {
                        if ((searchInp !== "") && (doc.name.toLowerCase().includes(searchInp.toLowerCase())))
                          return doc
                      }).map((doc) => {
                        return (
                          <>
                          <SearchResult myId={details.id} key={doc.id} friendlist={doc.friends} thisId={doc.userdoc} name={doc.name} desc={doc.desc} Time_to={doc.Time_to} Time_from={doc.Time_from}  consult_fees={doc.consult_fees} apnt_fees={doc.apnt_fees} avl={doc.availability} slug={doc.slug} myType={usertype}></SearchResult>
                            </>
                        )
                      })
                    }
                  </ul>
            </label>
                    </div>
              <div className={styles.user}>
              
                <img src={((info.profileImg)!==null)?info.profileImg:profile} width="80px" height="70px" alt="userprofile"/>
              </div>
            </div>
            {/* <Clock className={styles.clockside}></Clock> */}
            {(usertype === "Doctor") &&
              <div className={styles.reqlist}>
                <Requestlist myID={details.id} myUser={details.username} user_type={usertype}></Requestlist>
              </div>}
              <Posters userType={usertype} userid={details.id} username={details.username}></Posters>
             
           
          
         
       
        <footer className={style.footer} onClick={ToggleClass}>
        <div className={style.footer_left}>
          <h5 style={{'color':'#rgb(109 239 10)'}}>HOMO SAVIOUR - Live For Humanity</h5>
          <p>
             Homo Saviour-Live for Humanity is an Online Platform with
            the aim to help people in availing the facilities in Health,
            Agriculture and Education sector. Its main aim to reach out to the
            maximum number of people for their convenience in availing the
            facilities provided by the government as well as by the private
            organistaion. This is a platform for the people and by the
            people. Objective of this unit is to create an infrastructure where people from
            every class would be able to get a better health related services.
            Your contribution matters a lot .
            <Link to="/signup" style={{ color: "#00ffd0" }}>
              Join Us Now
            </Link>
          </p>
        </div>
        <ul className={style.footer_right}>
          <li>
            <h2>Links</h2>
            <ul className={style.box}>
              <li>
                <a >
                  About
                </a>
              </li>
              <li>
                <a
                >
                  Owner Profile
                </a>
              </li>
              <li>
                <a
                
                >
                  Contact Us
                </a>
              </li>
              <li>
                <a
                
                >
                  Report Bug
                </a>
              </li>
              {(localStorage.getItem('token')===null) && <li>
                <Link to="/signup">Join Us</Link>
              </li>}
            </ul>
          </li>
          <li>
            <h2>Important</h2>
            <ul className={style.box}>
              <li>
                <a
                
                >
                  Terms and Condition
                </a>
              </li>
              <li>
                <a
                 
                >
                  Services
                </a>
              </li>
              <li>
                <a
                
                >
                  Monetary Terms
                </a>
              </li>
            </ul>
          </li>
          <li>
            <h2>Official Address</h2>
            <ul className={style.box}>
              <li>
                <Link to="#">H No.12</Link>
              </li>
              <li>
                <Link to="#">Madhusudanpur</Link>
              </li>
              <li>
                <Link to="#">Bhagalpur</Link>
              </li>
              <li>
                <Link to="#">812005</Link>
              </li>
              <li>
                <Link to="#">Bihar</Link>
              </li>
            </ul>
          </li>
        </ul>
        <div className={style.footer_bottom}>
          <p>All Right reserved by &copy;Homo Saviour 2021</p>
        </div>
      </footer>
          </div>
          </div>
          </div>
      </>
      
    )
  else
    return(
      <>
      <Alert onClose={() => {setError(false)}} severity="error">
            <AlertTitle>Please Wait...</AlertTitle>
          </Alert>
      <CircularProgress disableShrink />
        <h1>!!! Authenticating Now...!!!</h1><br/>
        <Button variant="contained" color="primary" endIcon={<ReplayIcon></ReplayIcon>}onClick={()=> window.location.reload()}>Reload</Button><br />
        <Link className={style.link} onClick={handle_logout} to='/Login'>Log Out</Link>
        <footer>
          <Footer></Footer>
          </footer>
      </>
    )
}
export default Home;
