import {TextField } from '@material-ui/core'
import React, {useState, useEffect} from 'react'
import styles from '../style/appointment.module.css'
import profile from '../media/static/profile.png';
import { Avatar, Button, FormHelperText , CircularProgress} from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { useHistory } from 'react-router';
import axios from 'axios';
import { Alert, AlertTitle, Autocomplete } from '@material-ui/lab';
import Navtop from './Navtop';
import Pdf from './Pdf'
import $ from 'jquery'
import Footer from "../Landing/Footer";
import { Link } from 'react-router-dom';
import { serverip } from './constants'


function Appointment(props) {

   const history = useHistory()
  const [otherdata, setOtherdata] = useState([])
  const [userdata, setUserdata] = useState([])
  const [collector_list, setCollector_list] = useState([])
  const [myuser, setMyuser] = useState("")
  const [list, setList] = useState()
  const cost = 0;
  const [alert, setAlert] = useState(false)
  const [typealert, setTypealert] = useState(false)
  const [showreceipt, setShowreceipt] = useState(false)
  const [showpayform, setShowpayform] = useState(false)
  const [puid, setPuid] = useState("")
  const [mode, setMode] = useState('')
  const [refby, setRefby] = useState("None")
  const [patientName, setPatientName] = useState("")
  const [price, setPrice] = useState()
  const [otherid, setOtherid] = useState()
  const [collectorid, setCollectorid] = useState('')
  const [transactionid, setTransactionid] = useState("")
  const [loading, setLoading] = useState(true)
  const [booking_type, setBooking_type] = useState("Appointment")
  const [payerror, setPayerror] = useState(false)
  
  const username = props.match.params.user
  const other_type = props.match.params.type
  useEffect(async () => {
    
    await axios.get(`${serverip}/currentuser/`, {
      headers: {
        Authorization: `JWT ${localStorage.getItem('token')}`
      }
    }).then(async (respo) => {
      setMyuser(respo.data.username)
      setLoading(false)
      axios.all([
        await axios.get(`${serverip}/api/detail/${other_type}/${username}/`, {
          headers: {
            Authorization: `JWT ${localStorage.getItem('token')}`
          }
        }),
        await axios.get(`${serverip}/api/detail/Patient/${respo.data.username}`, {
          headers: {
            Authorization: `JWT ${localStorage.getItem('token')}`
          }
        })
      ]).then(axios.spread((other, user) => {
        
        setOtherdata(other.data);
        setUserdata(user.data);
        
      })
      )
    }).catch((error) => {
      const usererror = (JSON.stringify(error.message))
        if (usererror.includes("status code 404"))
          setTypealert(true)
        else (alert("Try again, Server Error"))
    })
        
  }, [username])



  var payjson = JSON.stringify({'payee':userdata.appuser , 'receiver': otherid, 'amount': cost, 'puid':puid, 'transactionid': "456845321654", 'mode':'online'})
  const makepayment = async () => {
    setPayerror(true)
    if (other_type === "Doctor") {
      setPrice(otherdata.apnt_fees)
      setOtherid(otherdata.appuser)
    }
    else if (other_type === "Hospital") {
      setPrice(otherdata.booking_price)
      setOtherid(otherdata.userhospital)
    }
    else if (other_type === "Patholab") {
      setPrice(totalcost)
      setOtherid(otherdata.userpatho)
    }
    else if (other_type === "Ambulance") {
      setPrice(otherdata.wic_charge)
      setOtherid(otherdata.userhospital)
    }
  //   await axios.post(`${serverip}/api/transaction/`, payjson, {
  //     headers: {
  //         'content-type': 'application/json',
  //         Authorization: `JWT ${localStorage.getItem('token')}`
  //     }
  //   }).then(res => {
  //     setShowreceipt(true)
  // })
  }  
  const showlist =async () => {
    await axios.get(`${serverip}/api/Collector/?search=${userdata.city}`, {
      headers: {
        Authorization: `JWT ${localStorage.getItem('token')}`
      }
    }).then(res => {
      setCollector_list(res.data)
    }).catch(err => {
      const error = (JSON.stringify(err.message))
      if (error.includes("status code 404"))
        setAlert(true)
        
    })
   
  }

  
  var totalcost = 0
  const calculate = () => {
    (list)&&
      list.forEach(element => {
          (otherdata.test_price).map(res => {
              if (element === res.test)
              totalcost = totalcost + parseInt(res.price)
            setPrice(totalcost)
                  })
      });
  }

  const handleSuccess = (res) => {
    // separate key and values from the res object which is nothing but param_dict
    let keyArr = Object.keys(res);
    let valArr = Object.values(res);

    // when we start the payment verification we will hide our Product form
    document.getElementById("container").style.display = "none";

    // Lets create a form by DOM manipulation
    // display messages as soon as payment starts
    let heading1 = document.createElement("h1");
    heading1.innerText = "Redirecting you to the paytm....";
    let heading2 = document.createElement("h1");
    heading2.innerText = "Please do not refresh your page....";
    document.body.appendChild(heading1);
    document.body.appendChild(heading2);

    $('#paymentform').submit()

    // //create a form that will send necessary details to the paytm
    // let frm = document.createElement("form");
    // frm.action = "https://securegw-stage.paytm.in/order/process/";
    // frm.method = "post";
    // frm.name = "paytmForm";

    // // we have to pass all the credentials that we've got from param_dict
    // keyArr.map((k, i) => {
    //   // create an input element
    //   let inp = document.createElement("input");
    //   inp.key = i;
    //   inp.type = "hidden";
    //   // input tag's name should be a key of param_dict
    //   inp.name = k;
    //   // input tag's value should be a value associated with the key that we are passing in inp.name
    //   inp.value = valArr[i];
    //   // append those all input tags in the form tag
    //   frm.appendChild(inp);
    // });

    // // append all the above tags into the body tag
    
    // document.body.appendChild(frm);
    // // finally submit that form
    // frm.submit();

    // // if you remember, the param_dict also has "'CALLBACK_URL': 'http://127.0.0.1:8000/api/handlepayment/'"
    // // so as soon as Paytm gets the payment it will hit that callback URL with some response and
    // // on the basis of that response we are displaying the "payment successful" or "failed" message
  };

  const startPayment = async () => {
   
    if (other_type === "Doctor") {
        if(booking_type==="Appointment")
        setPrice(otherdata.apnt_fees)
        else
          setPrice(otherdata.consult_fees)
        setOtherid(otherdata.userdoctor)
      }
      else if (other_type === "Hospital") {
        setPrice(otherdata.booking_price)
        setOtherid(otherdata.userhospital)
      }
      else if (other_type === "Patholab") {
        setPrice(totalcost)
        setOtherid(otherdata.userpatho)
      }
      else if (other_type === "Ambulance") {
        setPrice(otherdata.wic_charge)
        setOtherid(otherdata.userhospital)
      }
    
    
    let bodyData = new FormData();
    // send data to the backend
    bodyData.append("payeeslug", userdata.slug);
    bodyData.append("receiverslug", otherdata.slug);
    bodyData.append("puid", puid);
    bodyData.append("mode", 'online');
    bodyData.append("amount", price);
    // let bodyData = JSON.stringify({ 'payeeuser': parseInt(userdata.user), 'receiverid': parseInt(otherid), 'pid': puid, 'mop': 'online', 'price':price})
    await axios({
      url: `${serverip}/pay/`,
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      data: bodyData,
    }).then((res) => {
      // we will retrieve the param_dict that we are sending from the backend with
      // all the necessary credentials, and we will pass it to the handleSuccess() func 
     //  for the further process
      if (res) {
       setShowpayform(true)
      // handleSuccess(res.data.param_dict);
      // setShowreceipt(true)
    }
    });
  };
  if (!loading)
    return (
      <>
        <Navtop user={myuser} user_type={"Patient"}></Navtop>
        {typealert ? 
          (<Alert  onClose={() => {setTypealert(false)}} severity="error">
            <AlertTitle>Only Patient User can make booking</AlertTitle>
         <strong>Create an account with Patient User Type</strong>
          </Alert>) : null}
        {payerror ? 
          (<Alert  onClose={() => {setPayerror(false)}}>
            <AlertTitle>Payment Facility will be available after testing period is over</AlertTitle>
         <strong>!!! Thank You for joining us !!!</strong>
          </Alert>) : null}
        <div  className={styles.container} id="container">
            <div className={styles.header}>
                <div style={{'display':'flex'}}>
                    <ArrowBackIcon style={{"width": "62px", 'height':'36px'}} onClick={()=>history.goBack()}></ArrowBackIcon>
                    <Avatar className={styles.img} alt={userdata.name} src={(userdata.profileImg)?userdata.profileImg:profile} />
            </div>
              <h2 className={styles.headingmain}>HOMO SAVIOUR</h2>
                <Avatar className={styles.img} alt={otherdata.name} src={(otherdata.profileImg)?otherdata.profileImg:profile} />
                </div>
                <div className={styles.content} id="container">
            <div className={styles.timespan}>
              <select name="booking_type" onChange={(e) => {setBooking_type(e.target.value);(booking_type==="Appointment"?setPrice(otherdata.apnt_fees):setPrice(otherdata.consult_fees)) }} ><option value="Appointment" disabled>Choose Any One</option><option value="Appointment">Appointment</option><option value="Consult">Online Consult</option></select>
                    <div className={styles.spanBox}>Name : { userdata.name } </div><br/>
                        <div className={styles.spanBox}>city : { userdata.city }  </div><br/>
                       <div className={styles.spanBox}>State : { userdata.state }</div><br/>
                    <div className={styles.spanBox}>Phone: {userdata.phone }</div><br/>
                    <div className={styles.spanBox}>Patient Name:<br/>
                       <input type="text" onChange={(e)=>{setPatientName(e.target.value)}} className={styles.inpbox} /></div><br />
                       <div className={styles.spanBox}>ID Number :<br/> <input placeholder="Enter Correctly" className={styles.inpbox} onChange={(e)=>{setPuid(e.target.value)}} type="number" /></div><br/>
                        <div className={styles.spanBox} >Address :{ userdata.address }</div><br />
                        <div className={styles.btnBox}></div>  
          </div>
          {(other_type === "Doctor") &&  
            <div className={styles.details}>
              <div className={styles.spanBox}>Time From : {otherdata.Time_from}</div><br />
              <div className={styles.spanBox}>Time To :{otherdata.Time_to} </div><br />
              <div className={styles.spanBox}>city : {otherdata.city}  </div><br />
              <div className={styles.spanBox}>State : {otherdata.state}</div><br />
              <div className={styles.spanBox}> Name : {otherdata.name}</div><br />
              <div className={styles.spanBox}>Degree: {otherdata.desc}</div><br />
              <div className={styles.spanBox}>Specialisation : {otherdata.spcl}</div><br />
              <div className={styles.spanBox}>Appointment Fee :  &#8377;{otherdata.apnt_fees} </div><br />
              <div className={styles.spanBox} >Clinic Address: {otherdata.clinic_add}</div><br />
                      
            </div>}
          {
              (other_type === "Hospital") && 
              <div className={styles.details}>
              <div className={styles.spanBox}>city : {otherdata.city}  </div><br />
              <div className={styles.spanBox}>State : {otherdata.state}</div><br />
              <div className={styles.spanBox}> Name : {otherdata.name}</div><br />
              <div className={styles.spanBox}>Address: {otherdata.address}</div><br />
              <div className={styles.spanBox}>Available Bed : {otherdata.available_bed}</div><br />
              <div className={styles.spanBox}>Fee :  &#8377; {otherdata.booking_price} </div><br />
                      
            </div>}
          {
              (other_type === "Ambulance") && 
              <div className={styles.details}>
              <div className={styles.spanBox}>city : {otherdata.city}  </div><br />
              <div className={styles.spanBox}>State : {otherdata.state}</div><br />
              <div className={styles.spanBox}> Name : {otherdata.name}</div><br />
              <div className={styles.spanBox}>Address: {otherdata.service_area}</div><br />
              <div className={styles.spanBox}>Owner name : {otherdata.owner_name}</div><br />
              <div className={styles.spanBox}>Reg No : {otherdata.vehicle_reg}</div><br />
              <div className={styles.spanBox}>Fee :  &#8377; {otherdata.wic_charge} </div><br />
                      
            </div>}
            { (other_type === "Patholab") &&
            <>
             {alert ? 
          (<Alert onClose={() => {setAlert(false)}}>
            <AlertTitle>No Sample collector found in your area</AlertTitle>
        Let lab assign a sample Collector for you<strong>Have a Healthy Life !</strong>
          </Alert>):null}
                <div className={styles.details}>
              <div className={styles.spanBox}>city : {otherdata.city}  </div><br />
              <div className={styles.spanBox}>State : {otherdata.state}</div><br />
              <div className={styles.spanBox}> Name : {otherdata.name}</div><br />
                <div className={styles.spanBox}>Address: {otherdata.address}</div><br />
                <div className={styles.spanBox}>Sample Collector: {collectorid}</div><br />
              </div>
              <div>
                <div className={styles.spanBox}>Referred By:  </div><div id="select"><input type="text" name="refby" onChange={(e)=>setRefby(e.target.value) }/></div>
                {(otherdata.test_price)?
                  <>
                <div className={styles.spanBox}>Select Test:  </div><div id="select"><Autocomplete
                  freeSolo
                  multiple
                  onChange={(event, value) => setList(value)}
                  onInput={calculate}
                  disableClearable
                  options={(otherdata.test_price) && Object.values(otherdata.test_price).map((result) => result.test) }
        renderInput={(params) => (
            <TextField
            className={styles.userinp}
            {...params}
            variant="outlined"
                name="state"
                size="small"
                style={{ width: '100%', background: "#fff", padding:'1px 2px'}}
                        />)} /></div></> : <h1>No test available</h1>
                  
                  }
              <Button  variant="contained" color="primary" onClick={calculate}>Calculate</Button>
              <div><label>Total Cost:</label> &#8377; {price}</div>
                <Button variant="contained" color="primary" onClick={showlist}>Find a Sample Collector</Button>
                <FormHelperText>Leave Blank if you want lab to assign a Sample Collector</FormHelperText>
                
              
              {(collector_list.length) &&
                collector_list.map(collector => {
                  (collector.availability)?(
                  <div style={{'height':'300px', overflowY:'auto'}} onClick={()=>setCollectorid(collector.slug)} className={styles.top}>
                  <Avatar className={styles.img} alt={collector.name} src={(collector.profileImg)?collector.profileImg:profile} />
                    <div className={styles.title_text}> {collector.name}
                   <div className={styles.spcl}>{collector.city}</div>
                  </div> </div>): <p>No Sample Collector available now, Let your lab assign one</p>
                })}
               
            </div>
            <br />
            
            </>
            }    
            </div>
            <div  className={styles.btnbox} ><button onClick={makepayment}  id="paybtn" className={styles.btn}>Make Payment</button><button onClick={()=>history.goBack()} className={styles.btn}>Cancel</button>
                
                </div>
                <div className={styles.payment}>
                    
                </div>
        </div>
        
       
        {showpayform &&
          <div className={styles.formpay} >
            <form method="POST" action="https://securegw-stage.paytm.in/order/process/" id="paymentform">
              <div className={styles.spanbox}>
                <label htmlFor='payee'>Payee</label><input type="number" value={userdata.appuser} name='payee' />
              </div>
              <div className={styles.spanbox}>
                <label htmlFor='receiver'>Receiver</label><br /><input type="number" value={otherid} name='receiver' /><br />
                <span>Payee Name: {userdata.name}</span>
              </div>
              <div className={styles.spanbox}>
                <label htmlFor='puid'>ID no</label><br /><input type="number" value={otherid} name='receiver' />
              </div>
              <div className={styles.spanbox}>
                <label htmlFor='puid'>ID no</label><br /><select defaultValue="Online" onChange={(e) => setMode(e.target.value)} name='mode'>
                  <option value="Online">Online</option>
                  <option value="Offline">Offline</option>
                </select>
              </div>
              <div className={styles.spanbox}>
                <label htmlFor='amount'>Amount</label><br /><input type="number" value={price} name='amount' />
              </div>
              <button onClick={handleSuccess} variant="contained" color="primary" >Pay Now</button>
            </form>
          </div>
        }
    
        
        {(showreceipt) && (other_type==="Collector") &&
          <div >
            <Pdf mydata={userdata} rcvdata={otherdata} collector={collectorid} amnt={price} transid={transactionid} aadhar={puid} ptname={patientName}></Pdf>
          </div>}
        {(showreceipt) &&
          <div >
            <Pdf mydata={userdata} rcvdata={otherdata} amnt={price} transid={transactionid} aadhar={puid} ptname={patientName}></Pdf>
          </div>}
        </>
    )
    else
    return(
      <>
      <Alert onClose={() => {setLoading(false)}} severity="error">
            <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
          </Alert>
      <CircularProgress disableShrink />
        <h1>Authenticating Now...</h1><br/>
        <Button variant="contained" color="primary"onClick={()=> window.location.reload()}>Reload</Button><br />
        <Link to='/'>Log Out</Link>
        <footer>
          <Footer></Footer>
          </footer>
      </>
    )
}

export default Appointment
