import React , { useState, useEffect}from 'react'
import { Container, FormHelperText} from '@material-ui/core';
import axios from 'axios';
import styles from "../style/profile.module.css";
import profile from '../media/static/profile.png';
import {  TextField, Button, CircularProgress } from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import PublishIcon from '@material-ui/icons/Publish';
import Navtop from '../Landing/Navtop';
import JSONDATA from '../media/static/jsonFiles/statecity.json';
import { useHistory } from 'react-router';
import $ from 'jquery';
import Bankform from './Bankform';
import { Alert, AlertTitle } from '@material-ui/lab';
import { Link } from 'react-router-dom';
import Footer from '../Landing/Footer';
import {serverip} from '../Landing/constants'

function Collectreg(props) {
    const [profilePic, setProfilePic] = useState({ profile });
    const [colldata, setColldata] = useState({
        name: '',
        city: '',
        phone:''
    })
    const [post, setPost] = useState(false)
    const [username, setUsername] = useState(props.match.params.username)
    const [currUser, setCurrUser] = useState([])
    const [state, setState] = useState('')
    const [available, setAvailable] = useState()
    const [smplist, setSmplist] = useState([])
    const [loading, setLoading] = useState(true)
    const handleImg = (e) => {
        setProfilePic(e.target.files[0]);
    }
    if (username==="undefined")
        setUsername(localStorage.getItem('name'))
    var history = useHistory();

    const handleInput = (e) => {
        setColldata({ ...colldata, [e.target.name]: e.target.value })
    }
    const handleAvailability = (e) => {
        if (e.target.checked)
            setAvailable(true)

        else{
            setAvailable(false)
        }
    }
   
    useEffect(() => {
         axios.get(`${serverip}/api/detail/Collector/${username}`,{
            headers: {
              Authorization: `JWT ${localStorage.getItem('token')}`
            }
         }).then(json => {
            setLoading(false)
            setSmplist(json.data.sample_collected)
            setState(json.data.state)
            setColldata(json.data)
            getuser();
         }).catch(err => {
             const error = (JSON.stringify(err.message))
             if (error.includes("status code 404"))
                 setPost(true)
                 getuser();
              })
    }, [username])

    const getuser = async () => {
        await axios.get(`${serverip}/currentuser/`, {
            headers: {
              Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setCurrUser(res.data)
            setUsername(res.data.username)
        }).catch(history.push('/'))
           
  }
     var form_data = new FormData();
    
    form_data.append('name', colldata.name);
    form_data.append('state', state);
    form_data.append('city', colldata.city);
    if (post)
    form_data.append('usercollector', currUser.id);
    const putData = async () => {
        await axios.patch(`${serverip}/api/detail/Collector/${currUser.username}/`, form_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            $('subBtn').text('Submitted')
            setTimeout(() => {
                $('subBtn').text('Send')
            }, 2000);
        }).catch(error => {
            alert("Please Try again")
        })
    }
   
    // const uploadData = async () => {
    //     if ((colldata.sample_collected) !== null) {
    //         var datajson = JSON.stringify({
    //             "sample_collected": [...(colldata.sample_collected), sample]
    //         })
    //     }
    //      else {
    //         datajson = JSON.stringify({
    //             "sample_collected": [sample]
    //         })
    //         }
    //     await axios.patch(`http://127.0.0.1:8000/api/detail/Collector/${currUser.username}/`, datajson, {
    //         headers: {
    //             'content-type': 'application/json',
    //             Authorization: `JWT ${localStorage.getItem('token')}`
    //         }
    //     }).then(res => {
    //         $('#tpBtn').text('Uploaded')
    //         window.location.reload();
    //     })
    // }
    var datajs  = JSON.stringify(colldata, {state: state, usercollector: currUser.id}) 
    const postData = async () => {
        await axios.post(`${serverip}/api/Collector/`, form_data, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setPost(false)
        }).catch(error => {
            alert("Please Try again....Fill all the details")
        })
    }
    const img_data = new FormData();
    img_data.append('profileImg', profilePic)
    const upload = async () => {
        await axios.patch(`${serverip}/api/detail/Collector/${currUser.username}/`, img_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            },
           
        }).then($('#imgBtn').text('Uploaded'))
    }
  
    if(!loading)
    return (
        <>
        <Navtop user={currUser.username} user_type="Collector"></Navtop>
    <div className={styles.main}>
    <Container className={styles.container} maxWidth="lg">
        <div className={styles.top}>
            <div className={styles.imgBox}>
                            <img src={((colldata.profileImg) !== null) ? colldata.profileImg : profile} alt={colldata.name} />
            </div>
                <span className={styles.nameplate}><h1>{ colldata.name}</h1></span>
            </div>
            
        </Container>
        
        <form method="POST" encType="multipart/form-data">
        <div className={styles.grid}>
        <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>Name</h2>
                <input className={styles.userinp} type="text" onChange={handleInput}  placeholder={colldata.name}  name="name" required />
                    </div>
                <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>Phone No.</h2>
                    <input className={styles.userinp} placeholder={colldata.phone} type="number" id="phone" name="phone" onChange={handleInput} maxLength="10" required /></div>
                    <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>Profile Pic</h2>
                        <input type="file" id="fileImg" name="profileImg" accept="image/*" onChange={handleImg} />
                        <Button onClick={upload} variant="contained" id="imgBtn" color="secondary"> Upload </Button>

                  </div>
                <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>State</h2>
                <Autocomplete
                            freeSolo
                            value={state}
                            placeholder={state}
                            onChange={(event, value) => setState(value)} 
    disableClearable
    options={Object.keys(JSONDATA).map((result) => result)}
    renderInput={(params) => (
        <TextField
        className={styles.userinp}
        {...params}
        // margin="normal"
        variant="outlined"
            name="state"
            size="small"
            style={{ width: '100%', background: "#fff", padding:'1px 2px'}}
            placeholder={colldata.state}
        // InputProps={{ ...params.InputProps, type: 'search' }}
                            />)} /></div>
        <div className={styles.inpUser}>
      <h2 className={styles.nameLabel}>City</h2>
                        <input className={styles.userinp} placeholder={colldata.city} type="text" id={styles.city} name="city" onChange={handleInput} required  /></div>
                        {/* <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>ZIP code</h2>
                    <input className={styles.userinp} type="number" placeholder={colldata.zip_code} id="zip" onChange={handleInput}  name="zip_code" required /></div> */}
                  
                    
                     
                      <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>Available Now</h2>
                        <input className={styles.userinp} placeholder={colldata.availability} type="checkbox" id={styles.available} onChange={handleAvailability} name="availability" />
                        <FormHelperText> This can be changed at Homepage </FormHelperText></div>
                    <Button
                        id="subBtn"
    variant="contained"
    color="primary"
            endIcon={<PublishIcon></PublishIcon>}
            onClick={post? postData: putData }
  >
    Send
  </Button>
            </div>
            
                </form>
                
        </div>
        <div className={styles.testlist}>
            <div className={styles.tableData}>
            <table className={styles.table}>
                <thead>
<tr >
<th>Test Name</th>
<th>Patient Name</th>
<th>Patient Contact</th>
<th>Amount Collected</th>
<th>Patient username</th>
                    </tr></thead>
                <tbody>
                { 
                    (smplist.length)?
                    (smplist).map((testdata,index) => {
                        return (
                            <tr key={index}>
                                <td>{testdata.test}</td>
                                <td>{ testdata.patientName}</td>
                                <td>{ testdata.patientNo}</td>
                                <td> &#8377;{ testdata.amount}</td>
                                <td>{ testdata.userpatient}</td>
                </tr>
                        )
                    }):<tr><td>You have not collected any Sample yet</td></tr>
                    }
                 
                    </tbody>

            </table>
            </div>
            </div>
            <Bankform user={colldata.usercollector}></Bankform>
        </>
    )
    else
    return(
      <>
      <Alert onClose={() => {setLoading(false)}} severity="error">
            <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
          </Alert>
      <CircularProgress disableShrink />
        <h1>Authenticating Now...</h1><br/>
        <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
        <Link to='/'>Log Out</Link>
        <footer>
          <Footer></Footer>
          </footer>
      </>
    )
}

export default Collectreg
