import React, { useState, useEffect } from "react";
import { Container,  FormHelperText} from "@material-ui/core";
import axios from "axios";
import styles from "../style/profile.module.css";
import { TextField, Button, CircularProgress  } from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import PublishIcon from "@material-ui/icons/Publish";
import Navtop from "../Landing/Navtop";
import JSONDATA from "../media/static/jsonFiles/statecity.json";
import { useHistory } from "react-router";
import $ from "jquery";
import profile from "../media/static/profile.png";
import Bankform from './Bankform';
import Footer from "../Landing/Footer";
import { Alert, AlertTitle } from '@material-ui/lab';
import { Link } from 'react-router-dom';
import {serverip} from '../Landing/constants'
function Ambreg(props) {
  const [profilePic, setProfilePic] = useState({ profile });
  const [state, setState] = useState("");
  const [ambdata, setAmbdata] = useState([]);
  const [currUser, setCurruser] = useState([]);
  const [available, setAvailable] = useState();
  const [username, setUsername] = useState(props.match.params.username);
  const [post, setPost] = useState(false);
  const [loading, setLoading] = useState(true)

  var history = useHistory();
  const handleImg = (e) => {
    setProfilePic(e.target.files[0]);
    // ambdata.profileImg = profilePic
  };
  if (!username) {
    setUsername(localStorage.getItem("name"));
  }
  const handleInput = (e) => {
    setAmbdata({ ...ambdata, [e.target.name]: e.target.value });
  };
  const handleAvailability = (e) => {
        if (e.target.checked)
          setAvailable(true);
        else {
      setAvailable(false);
    }
  };

  var form_data = new FormData();

  form_data.append("name", ambdata.name);
  form_data.append("vehicle_reg", ambdata.vehicle_reg);
  form_data.append("phone", ambdata.phone);
  form_data.append("state", state);
  form_data.append("city", ambdata.city);
  form_data.append("address", ambdata.address);
  form_data.append("zip_code", ambdata.zip_code);
  form_data.append("wic_charge", ambdata.wic_charge);
  form_data.append("owner_name", ambdata.owner_name);
    if (post)
        form_data.append("useramb", currUser.id);

  useEffect(async () => {
       await axios.get(`${serverip}/api/detail/Ambulance/${username}`, {
        headers: {
          Authorization: `JWT ${localStorage.getItem("token")}`,
        },
      })
         .then((json) => {
          setLoading(false)
        setAmbdata(json.data);
        setState(json.data.state);
        setAvailable(json.data.availability);
        json.data.availability? $("#checkavl").attr("checked", true): $("#checkavl").attr("checked", false);
        
        getuser();
      })
      .catch((err) => {
        const error = JSON.stringify(err.message);
        if (error.includes("status code 404")) setPost(true);
        getuser();
      });
  }, [username]);

  const getuser = async () => {
    await axios.get(`${serverip}/currentuser/`, {
        headers: {
          Authorization: `JWT ${localStorage.getItem("token")}`,
        },
      })
      .then((requests) => {
        setCurruser(requests.data);
      })
      .catch((err) => {
        var error = JSON.stringify(err.message);
        if (error.includes("status code 401")) history.push("/login");
      });
  };
  const postData = async () => {
    await axios.post(`${serverip}/api/Ambulance/`, form_data, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `JWT ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        alert("Successful");
        setPost(false);
      })
      .catch((error) => {
        alert("Login again and try");
      });
  };
  var img_data = new FormData();
  img_data.append("profileImg", profilePic);
  const upload = async () => {
    await axios.patch(
        `${serverip}/api/detail/Ambulance/${username}/`,
        img_data,
        {
          headers: {
            "content-type": "multipart/form-data",
            Authorization: `JWT ${localStorage.getItem("token")}`,
          },
        }
      )
      .then(
        $("#uploadbtn").text("Uploaded")
      );
  };

  const putData = async () => {
    form_data.append('availability', available);
    await axios.put(`${serverip}/api/detail/Ambulance/${username}/`,form_data,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: `JWT ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((res) => {
        $('#btnsub').text("Updated")
        setTimeout(() => {
          $('#btnsub').text("Send")
        }, 2000);
      })
      .catch((error) => {
       alert('Login again and try.....Server Error')
      });
  };

  if(!loading)
  return (
    <>
      <Navtop user={currUser.username} user_type="Ambulance"></Navtop>
      <div className={styles.main}>
        <Container className={styles.container} maxWidth="lg">
          <div className={styles.top}>
            <div className={styles.imgBox}>
              <img
                src={ambdata.profileImg !== null ? ambdata.profileImg : profile}
                alt={ambdata.name}
              />
            </div>
            <span className={styles.nameplate}>
              <h1>AMBULANCE</h1>
            </span>
          </div>
        </Container>

        <form method="POST" encType="multipart/form-data">
          <div className={styles.grid}>
            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>Name of Driver</h2>
              <input
                className={styles.userinp}
                type="text"
                id="name"
                value={ambdata.name}
                name="name"
                onChange={handleInput}
                required
              />
            </div>
            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>Phone</h2>
              <input
                className={styles.userinp}
                value={ambdata.phone}
                type="number"
                id="phone"
                name="phone"
                onChange={handleInput}
                maxLength="10"
                required
              />
            </div>
            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>Profile Pic</h2>
              <input
                type="file"
                id="fileImg"
                name="profileImg"
                accept="image/*"
                onChange={handleImg}
              />
              <Button
                onClick={upload}
                variant="contained"
                color="secondary"
                id="uploadbtn"
              >
                {" "}
                Upload{" "}
              </Button>
            </div>
            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>Vehicle Registration No.</h2>
              <input
                className={styles.userinp}
                type="text"
                value={ambdata.vehicle_reg}
                onChange={handleInput}
                id="vehicleno"
                name="vehicle_reg"
                required
              />
            </div>
            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>State</h2>
              <Autocomplete
                freeSolo
                value={state}
                placeholder={state}
                onChange={(event, value) => setState(value)}
                disableClearable
                options={Object.keys(JSONDATA).map((result) => result)}
                renderInput={(params) => (
                  <TextField
                    className={styles.userinp}
                    {...params}
                    variant="outlined"
                    name="state"
                    size="small"
                    style={{
                      width: "100%",
                      background: "#fff",
                      padding: "1px 2px",
                    }}
                    value={ambdata.state}
                    // InputProps={{ ...params.InputProps, type: 'search' }}
                  />
                )}
              />
            </div>
            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>City</h2>
              <input
                className={styles.userinp}
                value={ambdata.city}
                type="text"
                required
                name="city"
                onChange={handleInput}
              />
            </div>
            <div className={styles.inpUser}>
              <h2 className="">Service Area</h2>
              <input
                name="address"
                type="text"
                style={{ height: "50px", width: "100%" }}
                value={ambdata.address}
                onChange={handleInput}
                required
              />
            </div>
            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>ZIP code</h2>
              <input
                className={styles.userinp}
                type="number"
                id="zip"
                onChange={handleInput}
                value={ambdata.zip_code}
                name="zip_code"
                required
              />
            </div>
            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>With in City Charge</h2>
              <input
                className={styles.userinp}
                type="number"
                onChange={handleInput}
                id="wic_charge"
                value={ambdata.wic_charge}
                name="wic_charge"
              />
              <FormHelperText>
                This can be changed -     
              </FormHelperText>
            </div>

            <div className={styles.inpUser}>
              <h2 className={styles.nameLabel}>Owner Name</h2>
              <input
                name="owner_name"
                id="owner"
                value={ambdata.owner_name}
                type="text"
                onChange={handleInput}
              ></input>
            </div>
            <div className={styles.data}>
              <h5>{available ? "Present - " : "Absent -  "}</h5>
            </div>
            <div className={styles.inpUser}>
              <h5 className={styles.nameLabel}>Availability -   </h5>
              <input
                className={styles.userinp}
                id="checkavl"
                type="checkbox"
                onChange={handleAvailability}
                name="availability"
            
              />
              <FormHelperText>
                {" "}
                This can be changed -     .{" "}
              </FormHelperText>
            </div>
            <Button
              variant="contained"
              color="primary"
              id="btnsub"
              endIcon={<PublishIcon></PublishIcon>}
              onClick={post ? postData : putData}
            >
              Send
            </Button>
          </div>
        </form>
      </div>
      <Bankform user={ambdata.useramb}></Bankform>
      <Footer></Footer>
    </>
    )
    else
    return(
      <>
      <Alert onClose={() => {setLoading(false)}} severity="error">
            <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
          </Alert>
      <CircularProgress disableShrink />
        <h1>Please Wait...or reload</h1><br/>
        <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
        <Link to='/'>Log Out</Link>
        <footer>
          <Footer></Footer>
          </footer>
      </>
    )
}

export default Ambreg;
