import React, {useState, useEffect} from 'react'
import styles from "../style/profile.module.css";
import axios from 'axios'
import Transreceipt from './Transreceipt'
import { Button } from '@material-ui/core';
import $, { param } from 'jquery'
import { useHistory } from 'react-router-dom';
import jsPDF from 'jspdf'
import { serverip } from './constants'

function Dashboard(props) {
    const [booklist, setBooklist] = useState([])
    const [showrcpt, setShowrcpt] = useState(false)
    const [Transdetail, setTransdetail] = useState([])
    const [datedata, setDatedata] = useState({})
    
    var total_amt = 0;
    var history =useHistory()
    // (param!=="undefined")? setUsername(props.match.params.username) : setUsername(props.user)
    
        
        const getalltrans = async () => {
            if (props.user !== "") {
                await axios.get(`${serverip}/api/rcv_transaction/${props.user}/`, {
                    headers: {
                      Authorization: `JWT ${localStorage.getItem('token')}`
                    }
                  }).then(res => {
                    setBooklist(res.data)
                })
            }
            else history.pushState('/')
        }
        
   
    const getdatetrans =async () => {
        await axios.get(`${serverip}/api/date_transaction/${datedata.datefrom}/${datedata.dateto}/${props.user}/`, {
                    headers: {
                      Authorization: `JWT ${localStorage.getItem('token')}`
                    }
                  }).then(res => {
                    setBooklist(res.data)
                })
    }
 
    async function receipt(e) {
            await axios.get(`${serverip}/api/detail/transaction/${e.target.id}/`, {
                headers: {
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
            }).then(res => {
                setTransdetail(res.data)
                setShowrcpt(true)
            })
            
    }
    const downloaddetail = () => {
        var doc = new jsPDF('l', 'px', [1366, 1366]);
        doc.setFontSize(5)
        doc.setTextColor(255,255,255)
        // doc.setTextColor(0, 0, 0); 
        // doc.setFillColor(255, 255, 255);
        var pdf = doc.html(document.querySelector('#transdetail'))
            pdf.save("Transactions.pdf");        
    }
    const handleInput = (e) => {
        setDatedata({ ...datedata, [e.target.name]: e.target.value })

    }
    const dateChange = () => {
        $('#date').val($('#date').val().replace(/(....).(..).(..)/, "$1-$2-$3"));
    }

    return (
        
        <div className={styles.bookingmain} style={{'height':'max-content'}} id="transdetail">
            <div className={styles.bookingtop}>
            <Button variant="contained" color="secondary" onClick={getalltrans}>Get All Transaction</Button>
            <Button variant="contained" color="secondary" style={{'marginLeft':'5px'}} onClick={downloaddetail}>Download Transactions</Button>
                <h1>My Transaction</h1>
                <div style={{'display':'flex', 'justifyContent':'space-evenly'}}>
                <div>
                <label for="datefrom">Date From: &nbsp;</label>
                    <input type="date" name="datefrom" onChange={dateChange, handleInput}></input></div>
                    <div>
                <label for="dateto">Date To: &nbsp;</label>
                    <input type="date" name="dateto" onChange={dateChange, handleInput}></input>
                    <Button variant="contained" color="secondary" onClick={getdatetrans}>Get Transaction</Button>
                    </div>
                    </div>
            </div>
            <div className={styles.bookingmid} style={{"overflowX":"auto"}}>
            <table className={styles.table} >
                    <thead>
                    <tr>
                        <th >No </th>
                        <th>Date</th>
                        <th>Time</th>
                        <th>Payee Name</th>
                        <th>Payee Username</th>
                        <th>Patient Name</th>
                        <th>Patient ID</th>
                        <th>Transaction id</th>
                        <th>Payment id</th>
                        <th>Amount</th>
                    </tr></thead>
                    <tbody>
                    { (booklist.length)?
                            (booklist).map((booking, index) => {
                                total_amt = total_amt + booking.amount;
                                var timeString = booking.time;
                                var H = + timeString.substr(0,2)
                                var h = H % 12 || 12;
                                var ampm = (H < 12 || H === 24) ? "AM" : "PM";
                                timeString = h + timeString.substr(2, 3)  + " "+ ampm;

                            return (
                                <tr key={index} >
                                    <td>{index+1}</td>
                                    <td>{booking.date}</td>
                                    <td>{timeString}</td>
                                    <td>{booking.payeename}</td>
                                    <td>{booking.payeeuser}</td>
                                    <td>{booking.patientname}</td>
                                    <td>{booking.puid}</td>
                                    <td>{booking.transactionid}</td>
                                    <td style={{'cursor':'pointer'}} onClick={receipt} id={booking.generatedid}>{booking.generatedid}</td>
                                    <td> &#8377;{booking.amount}</td>
                                </tr>
                            )
                            
                        }):<tr><td>No Transaction available</td></tr>
                        }
                    </tbody>
                </table>
                <div><label>Total Amount :</label>{ total_amt }</div>
                {(showrcpt) && <div><Button variant="contained" color="secondary" onClick={()=>{setShowrcpt(false)}}> Close</Button><Transreceipt mydata={Transdetail}></Transreceipt></div>}
            </div>
            </div>
    )
}

export default Dashboard
