import React, {useState, useEffect} from 'react'
import styles from "../style/profile.module.css";
import axios from 'axios'
import Transreceipt from './Transreceipt'
import { Button} from '@material-ui/core';
import { serverip } from './constants'
function Booking({ userid }) {
    const [booklist, setBooklist] = useState([])
    const [showrcpt, setShowrcpt] = useState(false)
    const [Transdetail, setTransdetail] = useState([])
    const getbookings = () => {
        if (userid !== "") {
            const gettrans = async () => {
                await axios.get(`${serverip}/api/user_transaction/${userid}/`, {
                    headers: {
                        Authorization: `JWT ${localStorage.getItem('token')}`
                    }
                }).then(res => {
                    setBooklist(res.data)
                })
            }
            gettrans()
        }
    }
 
    const receipt = async (e) => {
        await axios.get(`${serverip}/api/detail/transaction/${e.target.id}/`, {
            headers: {
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setTransdetail(res.data)
            setShowrcpt(true)
        })
            
    }
    if (booklist.length)
        return (
            <div className={styles.bookingmain}>
                <div className={styles.bookingtop}>
                    <h1>My Bookings</h1>
                </div>
                <div className={styles.bookingmid} style={{ "overflowX": "auto" }}>
                    <table className={styles.table} >
                        <thead>
                            <tr>
                                <th >No </th>
                                <th>Date</th>
                                <th>Payee Name</th>
                                <th>Receiver Name</th>
                                <th>Transaction id</th>
                                <th>Amount</th>
                            </tr></thead>
                        <tbody>
                            {(booklist.legth) ?
                                (booklist).map((booking, index) => {
                                    return (
                                        <tr key={index} >
                                            <td>{index}</td>
                                            <td>{booking.date}</td>
                                            <td>{booking.payeename}</td>
                                            <td>{booking.receivername}</td>
                                            <td style={{ 'cursor': 'pointer' }} onClick={receipt} id={booking.transactionid}>{booking.transactionid}</td>
                                            <td> &#8377;{booking.amount}</td>
                                        </tr>
                                    )
                            
                                }) : <tr><td>No Transaction available</td></tr>
                            }
                        </tbody>
                    </table>
                    {(showrcpt) && <div><Button variant="contained" color="secondary" onClick={() => { setShowrcpt(false) }}> Close</Button><Transreceipt mydata={Transdetail}></Transreceipt></div>}
                </div>
            </div>
        )
    else return (
        <div style={{'background':'greenyellow'}}>
        <Button variant="contained" color="secondary" onClick={getbookings}> Get your Booking details</Button> </div>
    )
}

export default Booking
