import axios from "axios";
import React, { useState} from "react";
import { TextField } from "@material-ui/core";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import styles from "../style/rowcard.module.css";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { FormHelperText } from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import JSONDATA from '../media/static/jsonFiles/statecity.json';
import { Link } from "react-router-dom";
import profile from '../media/static/profile.png';
import { serverip } from './constants'

const useStyles = makeStyles({
  root: {
    maxWidth: 300,
    display: FormHelperText,
  },
  
});


function Searchpatho(props) {
  const [stateName, setstateName] = useState("");
  const [city, setCity] = useState("");
  const [list, setList] = useState([]);
  
  async function Searchpatho(props) {
    if  (city !== "") {
      const requests = await axios.get(
        `${serverip}/api/Patholab/?search=${city}`,{
          headers: {
              Authorization: `JWT ${localStorage.getItem('token')}`    }
      });
      setList(requests.data);
      return requests;
    }
    else if ((city === "") && (stateName !== "")) {
      const requests = await axios.get(
        `${serverip}/api/Patholab/?search=${stateName}`,{
          headers: {
              Authorization: `JWT ${localStorage.getItem('token')}`    }
      });
      setList(requests.data);
      return requests;
    }
  }


  return (
    <React.Fragment>
      
      <div className={styles.bg}>
        <div className={styles.top}>
      <div className={styles.input}>
    <Autocomplete
            freeSolo
            defaultValue={stateName}
        id="state"
        disableClearable
        options={Object.keys(JSONDATA).map((result) => result)}
        renderInput={(params) => (
          <TextField
            {...params}
            label="Search By State"
            margin="normal"
            variant="outlined"
            name="stateName"
            onChange={(e) => { setstateName(e.target.value) }}
            InputProps={{ ...params.InputProps, type: 'search' }}
          />
          )}
      />
          <Button  variant="contained" color="primary" onClick={Searchpatho}>Search</Button>
        </div>
        <div className={styles.input}>
          <TextField
            label="City, Name or Local area"
            margin="normal"
            variant="outlined"
            name="city"
              onChange={(e) => { setCity(e.target.value) }} />
             <Button style={{margin : "16px"}} variant="contained" color="primary" onClick={Searchpatho}>Search</Button>
        </div>
        </div>
      
      <h2 className={styles.heading}>Patholabs IN {city}</h2>
      <div className={styles.row_doctors}>
          {(list.length)?
            list.map((patho) => {
              if (patho.availability) {
                return (
                  <Card className={` ${styles.row_doctor}`} style={{ overflow: "visible" }}>
                    <CardActionArea>
                      <LazyLoadImage
                        component="img"
                        alt={patho.name}
                        height="90"
                        style={{
                          height: "90",
                          width: "90",
                          backgroundPosition: "center",
                          backgroundRepeat: "no-repeat",
                          backgroundSize: "cover",
                        }}
                        key={patho.id}
                        src={(patho.profileImg) ? patho.profileImg : profile}
                        loading="lazy"
                        effect="blur"
                        title={patho.name}
                        className={styles.img}
                      />
                      <CardContent style={{ padding: '8px' }}>
                        <Typography
                          className={styles.title}
                          gutterBottom >
                          {patho.name}
                        </Typography>
                        <Typography
                          variant="body2"
                          color="textSecondary"
                          component="p"
                          className={styles.desc}
                        >
                          <div className={styles.TPlist} style={{ 'height': '50px', 'overflowY': 'scroll' }}><ul>{(patho.test_price !== null) ? (patho.test_price).map(tp => { return (<li style={{ 'borderBottom': '1px solid blue', 'color': '#fff' }}>Name:{tp.test} &#187; Price:&#8377;{tp.price}</li>) }) : null}</ul></div>
                        </Typography>
                        <span className={styles.uni}>{patho.address}</span>
                      </CardContent>
                    </CardActionArea>
                    <CardActions>
                      <div className={styles.item}>
                        <Link className={styles.btn} size="small" color="primary" to={{
                          pathname: `/appointment/Patholab/${patho.slug}`,
                        }}>
                          Book Now
                        </Link>
                      </div>
                      <div className={styles.item}>
                        <Link className={styles.btn} to={{
                          pathname: `/userprofile/${patho.slug}`,
                        }} size="small" color="primary">
                          VISIT PROFILE
                        </Link>
                      </div>
                    </CardActions>
                  </Card>
                )
              }
        }): (<h1>No available lab or diagnostic centre Found , will be available soon </h1>)
          }
      </div>
      </div>
      </React.Fragment>
  );
}

export default Searchpatho;