import React from 'react'
import jsPDF from 'jspdf'
import styles from '../style/appointment.module.css'
import { Button } from '@material-ui/core';


export default function Transreceipt(props) {
    const download = () => {
        var doc = new jsPDF('p', 'pt', 'a4');
        var pdf = doc.html(document.querySelector('#container'))
            pdf.save("mypdf.pdf");        
    }

    
    return (
        <>
        <div  id="container" style={{"textAlign":'center', 'width':'562px'}}>
            <h2 style={{'textAlign':'center'}}>Transaction Receipt</h2>
            <div className={styles.contpdf}>
            <div className={styles.left} >
                        <div className={styles.inp} ><label htmlFor="">Name :</label><span>{ props.mydata.payeename}</span> </div><br/>
                        <div className={styles.inp} ><label htmlFor="">payee username :</label><span> { props.mydata.payeeuser}</span>  </div><br/>
                       <div className={styles.inp} ><label htmlFor="">Receiver username :</label><span> { props.mydata.receiveruser}</span> </div><br/>
                    <div className={styles.inp} ><label htmlFor="">Receiver Name: </label><span>{ props.mydata.receivername}</span> </div><br/>
                    <div className={styles.inp} ><label htmlFor="">Patient Name: </label><span>{props.mydata.patientname}</span> </div><br/>
                    <div className={styles.inp} ><label htmlFor="">Amount: </label><span>{props.mydata.amount}</span> </div><br/>
                       <div className={styles.inp} ><label htmlFor="">ID Number : </label><span>{ props.mydata.puid}</span> </div><br/>
                        <div className={styles.inp}  ><label htmlFor="">Transaction Id :</label><span>{props.mydata.transactionid }</span> </div><br />
                        <div className={styles.inp}  ><label htmlFor="">Generated Id :</label><span>{props.mydata.generatedid }</span> </div><br />
                        <div className={styles.inp}  ><label htmlFor="">Date :</label><span>{props.mydata.date }</span> </div><br />
                        <div className={styles.inp}  ><label htmlFor="">Time :</label><span>{props.mydata.time }</span> </div><br />
                        
          </div>
             
            </div>
            <div className={styles.ulcont}>
            <ul>
                    <li>This is a computer generated receipt</li>
                    <li>Authentication shall be done during appointment</li>
                    <li>This does not guarantee the appointment</li>
                    <li>Transaction shall be considered null and void if receipt does not have a valid transaction ID</li>
                    <li>Patient must carry receipt during appointment</li>
                    <li>Patient must carry Aadhar Card with number provided during transaction</li>
                    <li>If Aadhar Number does not match with the one in the receipt, Transaction might get cancelled depending upon the payment receiver's choice</li>
                    <li>Appointment shall be cancelled if Patient could not reach on time as mentioned in the receipt, depending upon payment receiver's choice</li>
                    <li>100 % amount shall be refunded in any case of failure of appointment except scheduled timing failure </li>
                    <li>50 % amount shall be refunded in case Patient could not reach on time, and appointment is cancelled by the payment receiver </li>
                    <li>Refund shall be initiated instantly after appointment cancellation by payment receiver </li>
                    <li>Refund shall not be initiated in any case of illegal action or activity</li>
                    <li>Payment receiver shall be solely responsible for payment refund and transaction authentication</li>
                </ul>
                </div>
        </div>
        <Button variant="contained" color="secondary" onClick={download}> Download Receipt</Button>
        </>
    )
}
