import { Button, TextField } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import Bank from "../media/static/jsonFiles/Bank.json";
import styles from "../style/profile.module.css";
import axios from "axios";
import Autocomplete from "@material-ui/lab/Autocomplete";
import PublishIcon from "@material-ui/icons/Publish";
import $ from "jquery";
import { serverip } from '../Landing/constants'

function Bankform({ user }) {
  const [bankname, setBankname] = useState("");
  const [bankdata, setBankdata] = useState({
    usertype: "",
    holder_name: "",
    acc_number: "",
    conf_acc: "",
    ifsc: "",
  });
  const handle_acc_data = (e) => {
      setBankdata({ ...bankdata, [e.target.name]: e.target.value });
  };
  const accountupload = async () => {
    if ((bankdata.acc_number === bankdata.conf_acc) && (!bankdata.user)) {
        var datajson = JSON.stringify({
        user: user,
        bankname: bankname,
        usertype: bankdata.usertype,
        holder_name: bankdata.holder_name,
        acc_number: bankdata.acc_number,
        ifsc: bankdata.ifsc,
        });
      await axios
        .post(`${serverip}/api/acccreate/`, datajson, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `JWT ${localStorage.getItem("token")}`,
          },
        })
        .then((res) => {
          $("accbtn").text("Submitted");
          window.location.reload();
        })
        .catch(alert("Try again...Some error Occurred"));
    } else {
      alert("Account Number did not match or already have an account");
    }
  };
  const getbankdata = () => {
    axios
      .get(`${serverip}/api/accupdate/${user}`, {
        headers: {
          Authorization: `JWT ${localStorage.getItem("token")}`,
        },
      })
      .then((json) => {
        setBankdata(json.data);
          setBankname(json.data.bankname);
          $('#upBtn').css("display", "none");
      })
      .catch((err) => {
        const error = JSON.stringify(err.message);
        if (error.includes("status code 404"))
          alert("You have not updated your bank details");
        else alert("Some error Occurred, Try after Sometime");
      });
  };
  const updatebankdata = async () => {
    if (bankdata.acc_number === bankdata.conf_acc){
      var datajson = JSON.stringify({
        usertype: bankdata.usertype,
        holder_name: bankdata.holder_name,
        acc_number: bankdata.acc_number,
        ifsc: bankdata.ifsc,
      });
      await axios
        .patch(
          `${serverip}/api/accupdate/${bankdata.user}/`,
          datajson,
          {
            headers: {
              "content-type": "application/json",
              Authorization: `JWT ${localStorage.getItem("token")}`,
            },
          }
        )
        .then((res) => {
          $("#accbtn").text("Submitted");
          window.location.reload();
        })
        .catch((error) => {
          alert("Please Try again...some error occured");
        });
    } else alert("Account Number did not match");
  };
  return (
    <div style={{
          background: "linear-gradient(45deg, black, transparent)",
        textAlign: "center"
      }}>
      <a
        data-bs-toggle="modal"
        id="modalBtn"
        data-bs-target="#accountdetail"
        onClick={getbankdata}
        className={styles.acctogglebtn}
      >
        Add Account Details
      </a>
      <div
        className="modal fade"
        id="accountdetail"
        tabIndex="-1"
        aria-labelledby="AccountLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div
            className="modal-content"
            style={{
              background: "linear-gradient(45deg, black, transparent)",
            }}
          >
            <div className="modal-header" style={{ background: "#34f9ff" }}>
              <h5 className="modal-title" id="AccountLabel">
                Homo Saviour-Live For Humanity
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body" style={{ color: "white" }}>
              <h2>Add Your Account Detail</h2>
              <form className={styles.accform}>
                <div className={styles.inpform}>
                  <h5>Account Holder Name</h5>
                  <Autocomplete
                    value={bankname}
                    placeholder={bankname}
                    onChange={(event, value) => setBankname(value)}
                    freeSolo
                    options={Object.keys(Bank).map((result) => result)}
                    renderInput={(params) => (
                      <TextField
                        className={styles.userinp}
                        {...params}
                        value={bankname}
                        variant="outlined"
                        name="state"
                        size="small"
                        placeholder={bankname}
                        style={{
                          width: "100%",
                          background: "#fff",
                          padding: "1px 2px",
                        }}
                      />
                    )}
                  />
                </div>
                <div className={styles.inpform}>
                  <h5>Account Holder Name</h5>
                  <input
                    className="form-control"
                    onChange={handle_acc_data}
                    type="text"
                    name="holder_name"
                    placeholder={bankdata.holder_name}
                  />
                </div>
                <div className={styles.inpform}>
                  <h5>User Type</h5>
                  <select
                    id={styles.user_type}
                    name="usertype"
                    placeholder={bankdata.usertype}
                    onChange={handle_acc_data}
                    required
                  >
                    <option disabled="disabled" selected>
                      Choose any One
                    </option>
                    <option value="Doctor">Doctor</option>
                    <option value="Service">PSP</option>
                    <option value="Hospital">Hospital</option>
                    <option value="Ambulance">Ambulance</option>
                    <option value="Patholab">
                      Patholab and Diagnostic Centre
                    </option>
                    <option value="Collector">Employee</option>
                  </select>
                </div>
                <div className={styles.inpform}>
                  <h5>Account Number</h5>
                  <input
                    className="form-control"
                    onChange={handle_acc_data}
                    type="number"
                    onpaste={(e)=>{ e.target.value = ""; alert("Paste not allowed") }}
                    ondrop={(e)=>{ e.target.value = ""; alert("Paste not allowed") }}
                    autocomplete="off"
                    name="acc_number"
                    value={bankdata.acc_number}
                  />
                </div>
                <div className={styles.inpform}>
                  <h5>Confirm Account Number</h5>
                                  <input
                                      className="form-control"
                                      onChange={handle_acc_data}
                                      onpaste={(e) => { e.target.value = ""; alert("Paste not allowed") }}
                                      ondrop={(e) => { e.target.value = ""; alert("Paste not allowed") }}
                    autocomplete="off"
                    type="number"
                    name="conf_acc"
                  />
                </div>
                <div className={styles.inpform}>
                  <h5>IFSC Code</h5>
                  <input
                    className="form-control"
                    onChange={handle_acc_data}
                    type="text"
                    name="ifsc"
                    value={bankdata.ifsc}
                  />
                </div>
                <div
                  style={{ display: "flex", justifyContent: "space-evenly" }}
                >
                  <Button
                    id="upBtn"
                    variant="contained"
                    color="primary"
                    endIcon={<PublishIcon></PublishIcon>}
                    onClick={accountupload}
                  >
                    Upload
                  </Button>
                  <Button
                    id="subBtn"
                    variant="contained"
                    color="primary"
                    endIcon={<PublishIcon></PublishIcon>}
                    onClick={updatebankdata}
                  >
                    Update Data
                  </Button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Bankform;
