import Login from "./myComponents/Open/Login";
import Signup from "./myComponents/Open/Signup";
import Home from "./myComponents/Landing/Home";
import SearchRow from "./myComponents/Landing/SearchRow"
import React, { useState, useEffect } from 'react';
import { Route, Switch , Redirect} from "react-router";
import Info from "./myComponents/info/Info";
import Pathoreg from "./myComponents/info/Pathoreg";
import Ambreg from "./myComponents/info/Ambreg";
import Profile from './myComponents/Landing/Profile';
import Chat from './myComponents/chat/Chat'
import Appointment from "./myComponents/Landing/Appointment";
import Userprofile from "./myComponents/Landing/Userprofile";
import Hospitoreg from "./myComponents/info/Hospitoreg";
import Faq from "./myComponents/Landing/Faq";
import Faqlist from "./myComponents/Landing/Faqlist";
import Collectreg from "./myComponents/info/Collectreg";
import Dashboard from "./myComponents/Landing/Dashboard";
import {withRouter} from 'react-router';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';


function App() {
  const [logged_in, setLogged_in] = useState(localStorage.getItem('token') ? true : false)

    return (
      <React.Fragment>
         <Route exact path="/">
            {logged_in ? <Home/> : <Login />}
        </Route>
      <Route exact path="/Login" component={Login}/>
      <Route exact path="/Signup" component={Signup}/>
      
      <Route exact path="/search" component={SearchRow}/>
          <Route exact path="/profile/Patient/:username" component={Info} />
          <Route exact path="/profile/Doctor/:username" component={Profile} />
          <Route exact path="/consult/:id" component={withRouter(Chat)} />
          <Route exact path="/inbox" component={Chat} />
          <Route exact path="/appointment/:type/:user" component={Appointment} />
          <Route exact path="/profile/Ambulance/:username" component={Ambreg} />
          <Route exact path="/profile/Patholab/:username" component={Pathoreg} />
          <Route exact path="/profile/Hospital/:username" component={Hospitoreg} />
          <Route exact path="/profile/Collector/:username" component={Collectreg} />
          <Route exact path="/userprofile/:username" component={withRouter(Userprofile)} />
          <Route exact path="/Dashboard/:username" component={Dashboard} />
          <Route exact path="/forumarea/" component={withRouter(Faqlist)} />
          <Route exact path="/forum/:id" component={withRouter(Faq)} />

      
      <Route exact path="/home" component={Home}/>
      
      
      
        
      </React.Fragment>
    );
  }
// }




export default withRouter(App)
