import React, {useState} from 'react'
import styles from '../style/faq.module.css'
import axios from 'axios'
import $ from 'jquery'
import { makeStyles } from '@material-ui/core/styles';
import { Link } from "react-router-dom";
import { Accordion, AccordionDetails, AccordionSummary, Typography } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { serverip } from './constants'

const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: theme.typography.fontWeightRegular,
    },
  }));
function Faqlist({user}) {
    const [catblog, setCatblog] = useState([])
    const [cat, setCat] = useState()
    const [quecat, setQuecat] = useState()
    const [question, setQuestion] = useState("")
    const [logged_in, setLogged_in] = useState(localStorage.getItem('token') ? true : false)
    const classes = useStyles();

    const searchBlog = async () => {
        if(cat)
        await axios.get(`${serverip}/api/faq/?search=${cat}`,{
            headers: {
              Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setCatblog(res.data)
        })
        else alert("Please Select a Category")
    }

    
    const postdiscussion = async () => {
        var datajson = JSON.stringify({ question: question, category: quecat, byuser: user })
        let conf = window.confirm('Are you Sure to POST discussion')
        if (conf)
        {
            await axios.post(`${serverip}/api/faq/`, datajson, {
                headers: {
                    'content-type': 'application/json',
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
            }).then(res =>{
                $('#postbtn').text("Posted")
                $('#queinp').val("")
                $('#quecat').val("")

            })
            }
    }
    return (
        <div className={styles.faqmain}>
            <div className={styles.header}>
            <Accordion  style={{margin: "5px"}}>
                    <AccordionSummary
                        style={{'justifyContent':'space-between'}}
          className={styles.Accordian}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
                        <Typography className={classes.heading}>Rules</Typography>
                        <Link to='/'>Go to Home</Link>
        </AccordionSummary>
                    <AccordionDetails style={{"background": "linear-gradient(45deg, #5cd8dc, transparent)"}}>
                        <ol>
                    <li>No Spamming</li>
                    <li>Bad Language should not be used ia rage or anything as such</li>
                    <li>Only related discussion shall be done</li>
                    <li>Topic should not be repeated if already present</li>
                    <li>Only Doctors are allowed to post answer or discuss on a topic</li>
                    <li>Anyone involved in any unacceptable activities shall be banned </li>
                            </ol>
        </AccordionDetails>
      </Accordion>                
            </div>
            <div className={styles.srch}>
            <label htmlFor="category">Choose a Category:</label>
                <select name="category" id="cat" defaultValue="" onChange={(e)=>setCat(e.target.value)} required>
                <option value="" disabled>Choose any One</option>
                    <option value="General" >General ()</option>
                    <option value="Cardiology">Cardiology ( )</option>
                    <option value="ENT">ENT (,   )</option>
                    <option value="Gynaecology">Gynaecology ( )</option>
                    <option value="Paediatrics">Paediatrics (  )</option>
                    <option value="Physiology">Physiology ( )</option>
                    <option value="Orthopaedics">Orthopaedics (   )</option>
                    <option value="Diet">Diet ( )</option>
                    <option value="Fitness">Fitness</option>
                </select>
                <button onClick={searchBlog}>Search</button>
            </div>
            <div className={styles.bloglist}>
                <p>Category() : { cat}</p>
               
                <div className={styles.listanspart} style={{'padding':'7px','height':'40vh'}}>
                        <ul>
                            {
                                catblog.map(dta => {
                                    return (
                                        <Link style={{'textDecoration':'none'}} to={`/forum/${dta.id}`}><li><p>{ dta.question}</p><span style={{'fontSize':'13px', 'color':'grey'}}>By : { dta.byfullname} &nbsp; {dta.date} </span></li></Link>
                                    )
                                })
                            }
                           
                        </ul>
                </div>
            </div>
            {(logged_in) &&
                <div className={styles.quepart}>
                    <h1>Start a Discussion( -   )</h1>
                    <label htmlFor="category">Choose a Category:</label>
                    <select name="category" defaultValue="" id="quecat" onChange={(e) => setQuecat(e.target.value)} required>
                        <option value="" disabled>Choose any One</option>
                        <option value="General" >General ()</option>
                        <option value="Cardiology">Cardiology ( )</option>
                        <option value="ENT">ENT (,   )</option>
                        <option value="Gynaecology">Gynaecology ( )</option>
                        <option value="Paediatrics">Paediatrics (  )</option>
                        <option value="Physiology">Physiology ( )</option>
                        <option value="Orthopaedics">Orthopaedics ( )</option>
                    <option value="Diet">Diet ( )</option>
                    <option value="Fitness">Fitness</option>
                        </select>
                    <div className={styles.queinp}>
                        <label htmlFor="question">Topic ():</label>
                        <input type="text" id="queinp" maxLength="300" name="question" placeholder="     " required onChange={(e) => setQuestion(e.target.value)}></input>
                    </div>
                    <button className={styles.postbtn} id="postbtn" onClick={postdiscussion}>Post</button>

                </div>
            }
        </div>
    )
}

export default Faqlist
