import React , {useState, useEffect} from 'react'
import styles from '../style/prescription.module.css'
import axios from 'axios'
import { serverip } from './constants'

function Prescription(props) {
    const [presdata, setPresdata] = useState([])
    useEffect(async() => {
        await axios.get(`${serverip}/api/list_pres/${props.user}/`, {
                headers: {
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
        }).then(res => {
            setPresdata(res.data)
            })
    }, [props])
    const deletepres =async (e) => {
        var presid = e.target.id
        let conf = window.confirm('Are you Sure to delete this Prescription')
        if (conf) {
            await axios.delete(`${serverip}/api/delete_pres/${presid}`, {
                headers: {
                          Authorization: `JWT ${localStorage.getItem('token')}`
                        }
            }).then(res => {
                alert('Deleted')
            })
        }
        else return null
    }
    return (
        <>
        
        <div className={styles.prescription}>
            <h1 style={{'letterSpacing':'1px'}}>PRESCRIPTION</h1>
                {(presdata.length)?
                    presdata.map((presdta, index) => {
                        return (
                            <div key={presdta.id} className={styles.preslist}>
                                <div className={styles.namelbl} style={{ 'display': 'grid', 'gridTemplateColumns': '1fr 1fr' }}><span>For : {presdta.foruser}</span><span>From : Dr. { presdta.fromuser}</span></div>
                                <p style={{ 'fontSize': 'larger' }}>{ presdta.date}</p>
                        <table className={styles.table}>
               
                            <thead>
                   
                                <tr >
                                    <th>No</th>
                                    <th>Name</th>
                                    <th>Dose</th>
                                    <th>Timing</th>
                                    <th>Duration</th>
                                    <th>Del</th>
                                </tr></thead>
                                    <tbody>
                                        {(presdta.medpres.length) ? presdta.medpres.map(med => {
                                            <tr>
                                                <td>{index}</td>
                                                <td>{med.medname}</td>
                                                <td>{med.dose}</td>
                                                <td>{med.timing}</td>
                                                <td>{med.duration}</td>
                                                <i className="fa fa-trash" onClick={deletepres} id={presdta.id} aria-hidden="true"></i>
                                            </tr>
                                        }):<h1>No Prescription available</h1>
                                        }
                                
                                
                            </tbody>
  
                        </table>
                    </div>
                        )
                    }) :<>
                    <div className={styles.namelbl} style={{ 'display': 'grid', 'gridTemplateColumns': '1fr 1fr' }}><span>For : Patient Name</span><span>From : Dr. Doctor Name</span></div>
                        <p style={{ 'fontSize': 'larger' }}>2021-05-19</p>
                        <table className={styles.table}>
               
                    <thead>
                        <h5>Example</h5>
                        <tr >
                            <th>No</th>
                            <th>Name</th>
                            <th>Dose</th>
                            <th>Timing</th>
                            <th>Duration</th>
                            {/* <th>Del</th> */}
                        </tr></thead>
                            <tbody>
                       
                        <tr>
                            <td>1</td>
                            <td>Medicine</td>
                            <td>2 times</td>
                            <td>after lunch and dinner</td>
                            <td>15 days</td>
                            {/* <i className="fa fa-trash" onClick={deletepres}  aria-hidden="true"></i> */}
                            </tr>
                            </tbody>
                        </table>
                    </>
}
            </div>
            </>
    )
}

export default Prescription
