import React, { useState, useEffect } from "react";
import { Container, CircularProgress, Button, TextField } from "@material-ui/core";
import axios from "axios";
import styles from "../style/profile.module.css";
import profile from "../media/static/profile.png";
import Autocomplete from "@material-ui/lab/Autocomplete";
import PublishIcon from "@material-ui/icons/Publish";
import Navtop from "./Navtop";
import JSONDATA from "../media/static/jsonFiles/statecity.json";
import { Redirect } from "react-router";
import $ from "jquery";
import { Link } from "react-router-dom";
import Footer from "./Footer";
import Bankform from "../info/Bankform";
import { Alert, AlertTitle } from '@material-ui/lab'
import { serverip } from './constants'

function Profile(props) {
  const [profilePic, setProfilePic] = useState({ profile });
  const [available, setAvailable] = useState();
  const [pdata, setPdata] = useState({
    name: "",
    dob: "",
    phone: "",
    city: "",
    address: "",
    zip_code: "",
    desc: "",
    spcl: "",
    availability: available,
    apnt_fees: 0,
    consult_fees: 0,
    clinic_add: "",
  });
    
  const [state, setState] = useState("");
  const [post, setPost] = useState(false);
  const [username, setUsername] = useState(props.match.params.username);
  const [currUser, setCurrUser] = useState([]);
  const [loading, setLoading] = useState(true)

  const handleImg = (e) => {
    setProfilePic(e.target.files[0]);
  };
  const handleAvailability = (e) => {
    if (e.target.checked) setAvailable(true);
    else {
      setAvailable(false);
    }
  };
  pdata.availability
    ? $("#checkavl").attr("checked", true)
    : $("#checkavl").attr("checked", false);

  const handleInput = (e) => {
    setPdata({ ...pdata, [e.target.name]: e.target.value });
  };
 
  var form_data = new FormData();
  form_data.append("name", pdata.name);
  form_data.append("phone", pdata.phone);
  form_data.append("state", state);
  form_data.append("city", pdata.city);
  form_data.append("address", pdata.address);
  form_data.append("zip_code", pdata.zip_code);
  form_data.append("apnt_fees", pdata.apnt_fees);
  form_data.append("consult_fees", pdata.consult_fees);
  form_data.append("desc", pdata.desc);
  form_data.append("spcl", pdata.spcl);
  form_data.append("clinic_add", pdata.clinic_add);
  form_data.append("availability", available);
  var img_data = new FormData();
  img_data.append("profileImg", profilePic);
  if (post) {
    form_data.append("userdoc", currUser.id);
    form_data.append("dob", pdata.dob);
  }

  useEffect(() => {
    axios
      .get(`${serverip}/api/detail/Doctor/${username}`, {
        headers: {
          Authorization: `JWT ${localStorage.getItem("token")}`,
        },
      })
      .then((json) => {
        json.data.availability
          ? $("#checkavl").attr("checked", "checked")
          : $("#checkavl").attr("checked", false);
        setPdata(json.data);
        setState(json.data.state);
        setAvailable(json.data.availability);
        getuser();
        
      })
      .catch((err) => {
        const error = JSON.stringify(err.message);
        if (error.includes("status code 404")) setPost(true);
        getuser();
      });
  }, [props]);
  const putData = async () => {
    await axios
      .patch(
        `${serverip}/api/detail/Doctor/${currUser.username}/`,
        form_data,
        {
          headers: {
            "content-type": "multipart/form-data",
            Authorization: `JWT ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((res) => {
        $("#subBtn").text("Submitted");
        window.location.reload();
      })
      .catch((error) => {
        alert("Please Try again...some error occured");
      });
  };
  const getuser = async () => {
    await axios
      .get(`${serverip}/currentuser/`, {
        headers: {
          Authorization: `JWT ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        setCurrUser(res.data);
        setLoading(false)
      })
      .catch((err) => {
        const error = JSON.stringify(err.message);
        return (
          <Redirect
            to={{
              pathname: `/login`,
            }}
          />
        );
      });
  };
  const postData = async () => {
    await axios
      .post(`${serverip}/api/Doctor/`, form_data, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `JWT ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        setPost(false);
        $("subBtn").text("Submitted");
        window.location.reload();
      })
      .catch(alert("Try again...Some error Occurred"));
  };
  const upload = async () => {
    await axios
      .patch(
        `${serverip}/api/detail/Doctor/${currUser.username}/`,
        img_data,
        {
          headers: {
            "content-type": "multipart/form-data",
            Authorization: `JWT ${localStorage.getItem("token")}`,
          },
        }
      )
      .then($("#uploadBtn").text("Uploaded"));
  };
  const dateChange = () => {
    $("#date").val(
      $("#date")
        .val()
        .replace(/(....).(..).(..)/, "$1-$2-$3")
    );
  };
  if (currUser.username === username)
    return (
      <>
        <Navtop user={currUser.username} user_type="Doctor"></Navtop>
        <div className={styles.main}>
          <Container className={styles.container} maxWidth="lg">
            <div className={styles.top}>
              <div className={styles.imgBox}>
                <img src={pdata.profileImg} alt="profile"></img>
              </div>
              <span className={styles.nameplate}>
                <h1>{pdata.name}</h1>
              </span>
            </div>
          </Container>

          <form method="POST" encType="multipart/form-data">
            <div className={styles.grid}>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Name</h2>
                <input
                  className={styles.userinp}
                  type="text"
                  onChange={handleInput}
                  value={pdata.name}
                  placeholder={pdata.name}
                  id="name"
                  name="name"
                  required
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Phone No.</h2>
                <input
                  className={styles.userinp}
                  placeholder={pdata.phone}
                  type="number"
                  value={pdata.phone}
                  name="phone"
                  onChange={handleInput}
                  maxLength="10"
                  required
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Date of Birth</h2>
                <input
                  className={styles.userinp}
                  type="date"
                  onChange={(dateChange, handleInput)}
                  name="dob"
                  required
                />
                <span>{pdata.dob}</span>
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Profile Pic</h2>
                <input
                  type="file"
                  id="fileImg"
                  name="profileImg"
                  accept="image/*"
                  onChange={handleImg}
                />
                <Button
                  onClick={upload}
                  variant="contained"
                  id="uploadBtn"
                  color="secondary"
                >
                  {" "}
                  Upload{" "}
                </Button>
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>State</h2>
                <Autocomplete
                  value={state}
                  placeholder={state}
                  onChange={(event, value) => setState(value)}
                  freeSolo
                  options={Object.keys(JSONDATA).map((result) => result)}
                  renderInput={(params) => (
                    <TextField
                      className={styles.userinp}
                      {...params}
                      value={state}
                      variant="outlined"
                      name="state"
                      size="small"
                      placeholder={state}
                      style={{
                        width: "100%",
                        background: "#fff",
                        padding: "1px 2px",
                      }}
                    />
                  )}
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>City</h2>
                <input
                  className={styles.userinp}
                  value={pdata.city}
                  placeholder={pdata.city}
                  type="text"
                  id={styles.city}
                  required
                  name="city"
                  onChange={handleInput}
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className="">Local Area</h2>
                <textarea
                  name="address"
                  value={pdata.address}
                  placeholder={pdata.address}
                  style={{ width: "100%" }}
                  onChange={handleInput}
                  cols="30"
                  rows="3"
                  required
                ></textarea>
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>ZIP code</h2>
                <input
                  className={styles.userinp}
                  type="number"
                  placeholder={pdata.zip_code}
                  value={pdata.zip_code}
                  onChange={handleInput}
                  name="zip_code"
                  required
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Degree</h2>
                <input
                  className={styles.userinp}
                  type="text"
                  placeholder={pdata.desc}
                  onChange={handleInput}
                  value={pdata.desc}
                  name="desc"
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Specialization Field</h2>
                <input
                  className={styles.userinp}
                  type="text"
                  value={pdata.spcl}
                  onChange={handleInput}
                  placeholder={pdata.spcl}
                  name="spcl"
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Appointment Fee</h2>
                <input
                  className={styles.userinp}
                  type="number"
                  value={pdata.apnt_fees}
                  placeholder={pdata.apnt_fees}
                  onChange={handleInput}
                  id="apntfee"
                  name="apnt_fees"
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Consult Fee</h2>
                <input
                  className={styles.userinp}
                  type="number"
                  value={pdata.consult_fees}
                  placeholder={pdata.consult_fees}
                  onChange={handleInput}
                  name="consult_fees"
                />
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Clinic Address</h2>
                <textarea
                  name="clinic_add"
                  placeholder={pdata.clinic_add}
                  value={pdata.clinic_add}
                  style={{ width: "100%" }}
                  id="clinic"
                  cols="3"
                  onChange={handleInput}
                  rows="2"
                ></textarea>
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Availability</h2>
                <div className={styles.data}>
                  <h3>
                    {pdata.availability
                      ? "Present - "
                      : "Absent -  "}
                  </h3>
                </div>
              </div>
              <div className={styles.inpUser}>
                <h2 className={styles.nameLabel}>Set Availability</h2>
                <input
                  className={styles.userinp}
                  id="checkavl"
                  type="checkbox"
                  onClick={handleAvailability}
                  name="availability"
                />
              </div>
              <Button
                id="subBtn"
                variant="contained"
                color="primary"
                endIcon={<PublishIcon></PublishIcon>}
                onClick={post ? postData : putData}
              >
                Send
              </Button>
             
            </div>
          </form>
            </div>
            <Bankform user={pdata.userdoc}></Bankform>
        <Footer></Footer>
       
      </>
    );
    else
    return(
      <>
      <Alert onClose={() => {setLoading(false)}} severity="error">
            <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
          </Alert>
      <CircularProgress disableShrink />
        <h1>Authenticating Now...</h1><br/>
        <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
        <Link to='/'>Home</Link>
        <footer>
          <Footer></Footer>
          </footer>
      </>
    )
}

export default Profile;
