import React , { useState, useEffect}from 'react'
import { Container, FormHelperText} from '@material-ui/core';
import axios from 'axios';
import styles from "../style/profile.module.css";
import profile from '../media/static/profile.png';
import { TextField, Button, CircularProgress } from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import PublishIcon from '@material-ui/icons/Publish';
import Navtop from '../Landing/Navtop';
import JSONDATA from '../media/static/jsonFiles/statecity.json';
import { Redirect} from 'react-router';
import $ from 'jquery';
import { Alert, AlertTitle } from '@material-ui/lab';
import { Link } from 'react-router-dom';
import Bankform from './Bankform';
import Footer from '../Landing/Footer';
import { serverip } from '../Landing/constants'

function Hospitoreg(props) {
    const [profilePic, setProfilePic] = useState({ profile });
    const [error, setError] = useState(false)
    const [available, setAvailable] = useState()
    const [state, setState] = useState('')
    const [loading, setLoading] = useState(true)
    const [hdata, setHdata] = useState({
        name: '',
        city: '',
        address: '',
        total_bes: '',
        availability: available,
        available_bed: 0,
        booking_price: 0,
    })
    const [post, setPost] = useState(false)
    const username = (props.match.params.username)
    const [currUser, setCurrUser] = useState([])
    const handleImg = (e) => {
        setProfilePic(e.target.files[0]);
    }
    const handleInput = (e) => {
        setHdata({ ...hdata, [e.target.name]: e.target.value })
    }
    
    const handleAvailability = (e) => {
        if (e.target.checked)
            setAvailable(true)

        else {
            setAvailable(false)
        }
    }
    (hdata.availability) ? $('#checkavl').attr('checked', true) : $('#checkavl').attr('checked', false)
    
    var form_data = new FormData();
    form_data.append('name', hdata.name);
    form_data.append('state', state);
    form_data.append('city', hdata.city);
    form_data.append('address', hdata.address);
    form_data.append('total_bes', hdata.total_bes);
    form_data.append('available_bed', hdata.available_bed);
    form_data.append('booking_price', hdata.booking_price);
    form_data.append('availability', available);
    if (post)
        form_data.append('userhospital', currUser.id);

    useEffect(() => {
        axios.get(`http://127.0.0.1:8000/api/detail/Hospital/${username}`, {
            headers: {
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(json => {
            setLoading(false)
            setAvailable(json.data.availability)
            setHdata(json.data)
            setState(json.data.state)
            
            getuser()
        }).catch(err => {
            const error = (JSON.stringify(err.message))
            if (error.includes("status code 404"))
                setPost(true)
            getuser();
        })
    }, [username])
    
    const putData = async () => {
        await axios.patch(`${serverip}/api/detail/Hospital/${currUser.username}/`, form_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(window.location.reload()).catch(setError(true)
        )
    }
    
    const getuser = async () => {
        await axios.get(`${serverip}/currentuser/`, {
            headers: {
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setCurrUser(res.data)
        }).catch(err => {
            return (
                <Redirect to={{
                    pathname: `/login`
                }}
                />
            )
                
        })
           
    }
    const postData = async () => {
        await axios.post(`${serverip}/api/Hospital/`, form_data, {
            headers: {
                'Content-Type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setPost(false)
        }).catch(err => {
            setError(true)
        
        }
        )
    }
    const upload = async () => {
        const img_data = new FormData();
        img_data.append('profileImg', profilePic)
        await axios.patch(`${serverip}/api/detail/Hospital/${username}/`, img_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            },
           
        }).then(window.location.reload())
    }

    if ((!loading) && (currUser.username === username))
        return (
            <>
                <Navtop user={currUser.username} user_type="Hospital"></Navtop>
                <div className={styles.main}>
                    {error ?
                        (<Alert onClose={() => { setError(false) }} severity="error">
                            <AlertTitle>Try after Sometime </AlertTitle>
                            <strong>Server Down</strong>
                        </Alert>) : null}
                    <Container className={styles.container} maxWidth="lg">
                        <div className={styles.top}>
                            <div className={styles.imgBox}>
                                <img src={((hdata.profileImg) !== null) ? hdata.profileImg : profile} alt={hdata.name} />
                            </div>
                            <span className={styles.nameplate}><h1>{hdata.name}</h1></span>
                        </div>
                
                    </Container>
            
                    <form method="POST" encType="multipart/form-data">
                        <div className={styles.grid}>
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>Hospital Name</h2>
                                <input className={styles.userinp} type="text" onChange={handleInput} placeholder={hdata.name} name="name" required />
                            </div>
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>Phone No.</h2>
                                <input className={styles.userinp} placeholder={hdata.phone} type="number" name="phone" onChange={handleInput} maxLength="10" required /></div>
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>Profile Pic</h2>
                                <input type="file" id="fileImg" name="profileImg" accept="image/*" onChange={handleImg} />
                                <Button onClick={upload} variant="contained" color="secondary"> Upload </Button>

                            </div>
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>State</h2>
                                <Autocomplete freeSolo id="state" disableClearable
                                    options={Object.keys(JSONDATA).map((result) => result)}
                                    value={state}
                                    placeholder={state}
                                    onChange={(event, value) => setState(value)}
                                    renderInput={(params) => (
                                        <TextField
                                            className={styles.userinp}
                                            {...params}
                                            // margin="normal"
                                            variant="outlined"
                                            name="state"
                                            size="small"
                                            style={{ width: '100%', background: "#fff", padding: '1px 2px' }}
                                            value={state}
                                            placeholder={state}
                                        // InputProps={{ ...params.InputProps, type: 'search' }}
                                        />)} /></div>
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>City</h2>
                                <input className={styles.userinp} placeholder={hdata.city} type="text" id={styles.city} required name="city" onChange={handleInput} /></div>
                            <div className={styles.inpUser}>
                                <h2 className="">Local Area</h2>
                                <textarea name="address" placeholder={hdata.address} style={{ width: '100%' }} onChange={handleInput} cols="30" rows="3" required></textarea></div>
                            {/* <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>ZIP code</h2>
                        <input className={styles.userinp} type="number" placeholder={hdata.zip_code} id="zip" onChange={handleInput}  name="zip_code" required /></div> */}
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>Total Beds</h2>
                                <input className={styles.userinp} type="text" placeholder={hdata.total_bes} onChange={handleInput} name="total_bes" />
                                <FormHelperText> This can be changed at Homepage </FormHelperText></div>
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>Available Beds</h2>
                                <input className={styles.userinp} type="text" placeholder={hdata.available_bed} onChange={handleInput} name="available_bed" /></div>
                         
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>Booking Charge</h2>
                                <input className={styles.userinp} type="number" placeholder={hdata.booking_price} onChange={handleInput} name="booking_price" />
                                <FormHelperText> This can be changed at Homepage </FormHelperText></div>
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>Availability</h2>
                                <div className={styles.data}><h3>{(hdata.availability) ? "Present - " : "Absent -  "}</h3></div></div>
                            <div className={styles.inpUser}>
                                <h2 className={styles.nameLabel}>Set Availability</h2>
                                <input className={styles.userinp} id='checkavl' type="checkbox" onClick={handleAvailability} name="availability" />
                                <FormHelperText> This can be changed at Homepage </FormHelperText></div>
                            <Button
                                variant="contained"
                                color="primary"
                                endIcon={<PublishIcon></PublishIcon>}
                                onClick={post ? postData : putData}
                            >
                                Send
                            </Button>
                        </div>
                
                    </form>
                    
                </div>
                <Bankform user={hdata.userhospital}></Bankform>
            </>
        )
        else
        return(
          <>
          <Alert onClose={() => {setError(false)}} severity="error">
                <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
              </Alert>
          <CircularProgress disableShrink />
            <h1>Authenticating Now...</h1><br/>
            <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
            <Link to='/'>Home</Link>
            <footer>
              <Footer></Footer>
              </footer>
          </>
        )
}

export default Hospitoreg
