import React, {useState} from 'react';
import {  makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import styles from './posters.css';
import card1 from '../media/static/ambulance.jpg';
import card2 from '../media/static/cardDoctor.jpg';
import card3 from '../media/static/cardpatho.jpg';
import RowCard from "./RowCard";
import SearchRow from './SearchRow';
import Searchpatho from './Searchpatho';
import $ from 'jquery';
import Searchhospital from './Searchhospital';
import SearchAmbulance from './SearchAmbulance';
import Faqlist from './Faqlist';
import Booking from './Booking';
import Dashboard from './Dashboard';
import Prescription from './Prescription';





export default function Posters({ userType, userid, username }) {
  const [showTrans, setShowTrans] = useState(false)
  const [showPres, setShowPres] = useState(false)
  const usestyles = makeStyles((theme) => ({
    root: {
      width: '100%',
    },
    heading: {
      fontSize: theme.typography.pxToRem(20),
      fontWeight: theme.typography.fontWeightRegular,
    },
  }));
  const classes = usestyles();
  const [expanded, setExpanded] = React.useState('panel1');

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };
  const topFunction = (e) => {
    $('html, body').animate({ scrollTop: 10  }, 'fast')
  }
  

  return (
    <div className={classes.root}>
      <Accordion  style={{ 'margin': "10px", 'background':'linear-gradient(46deg, #b21ebd, #d1e624a3)' }} square expanded={expanded === 'panel1'} onChange={handleChange('panel1')} onClick={$('this').scrollTop()}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
          className={styles.Accordian}
        >
          <Typography className={`${classes.heading}, ${styles.heading}`}>Services</Typography>
        </AccordionSummary>
        <AccordionDetails>
        <div className="cards" >
            <div className="cardBox">
            <RowCard title="Ambulance 24*7" desc="the primary providers of a 24/7 response to medical and trauma related emergencies.  Medical staff available inside ambulances are well trained..." imgCard={card1}
                ></RowCard>
            </div>
            <div className="cardBox">
            <RowCard title="Doctors 24*7" desc="All renowned doctors from all over INDIA at your fingertips for all related emergencies.  Well trained medical staff for essential  service..." imgCard={card2}
            ></RowCard>
            </div>
            <div className="cardBox">
            <RowCard title="PathoLab and Diagnostic Centre 24*7" desc="At Home service, Get your report from all  the leading test laboratory of India.  Sample pick up service available 24*7 with complete care and hygiene... " imgCard={card3}
        ></RowCard>
                
        </div>
        </div>
        </AccordionDetails>
      </Accordion>
      <Accordion  square expanded={expanded === 'panel2'} onChange={handleChange('panel2')} style={{ 'margin': "10px", 'background':'linear-gradient(46deg, #b21ebd, #d1e624a3)' }}>
        <AccordionSummary
          className={styles.Accordian}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography  className={`${classes.heading}, ${styles.heading}`}>Search Doctors</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <SearchRow myID={userid}> </SearchRow>
        </AccordionDetails>
      </Accordion>
      <Accordion square expanded={expanded === 'panel3'} style={{ 'margin': "10px", 'background':'linear-gradient(46deg, #b21ebd, #d1e624a3)' }} onChange={handleChange('panel3')}  >
        <AccordionSummary
          className={styles.Accordian}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography className={classes.heading}>Search Patholabs and Diagnostic Centre</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Searchpatho></Searchpatho>
        </AccordionDetails>
      </Accordion>
      <Accordion square expanded={expanded === 'panel4'} style={{ 'margin': "10px", 'background':'linear-gradient(46deg, #b21ebd, #d1e624a3)' }} onChange={handleChange('panel4')}  >
        <AccordionSummary
          className={styles.Accordian}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel4a-content"
          id="panel4a-header"
        >
          <Typography className={classes.heading}>Search Hospital</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Searchhospital> </Searchhospital>
        </AccordionDetails>
      </Accordion>
      <Accordion square expanded={expanded === 'panel5'} style={{ 'margin': "10px", 'background':'linear-gradient(46deg, #b21ebd, #d1e624a3)' }} onChange={handleChange('panel5')}  >
        <AccordionSummary
          className={styles.Accordian}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel5a-content"
          id="panel4a-header"
        >
          <Typography className={classes.heading}>Search for Ambulance</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <SearchAmbulance> </SearchAmbulance>
        </AccordionDetails>
      </Accordion>
      <Accordion  square expanded={expanded === 'panel6'} onChange={handleChange('panel6')} style={{ 'margin': "10px", 'background':'linear-gradient(46deg, #b21ebd, #d1e624a3)' }} >
        <AccordionSummary
          className={styles.Accordian}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel6a-content"
          id="panel5a-header"
        >
          <Typography className={classes.heading}>FAQs and FORUM</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Faqlist user={userid} username={username}></Faqlist>
        </AccordionDetails>
      </Accordion>
      <Accordion square expanded={expanded === 'panel7'} onClick={(e) => { setShowTrans(true)}} onChange={handleChange('panel7')} style={{ 'margin': "10px", 'background':'linear-gradient(46deg, #b21ebd, #d1e624a3)' }} >
        <AccordionSummary
          className={styles.Accordian}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel7a-content"
          id="panel6a-header"
        >
          <Typography className={classes.heading}>Transaction and Bookings</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {((userType==="Doctor") || (userType==="Patholab") || (userType==="Hospital") && (showTrans) )?<Dashboard user={username}></Dashboard> :<Booking userid={userid}></Booking>}
        </AccordionDetails>
      </Accordion>
      { (userType === "Patient")&&
        <Accordion square expanded={expanded === 'panel8'} onClick={(e) => { setShowPres(true)}} onChange={handleChange('panel8')} style={{ 'margin': "10px", 'background': 'linear-gradient(46deg, #b21ebd, #d1e624a3)' }} >
          <AccordionSummary
            className={styles.Accordian}
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel8a-content"
            id="panel8a-header"
          >
            <Typography className={classes.heading}>Prescriptions</Typography>
          </AccordionSummary>
          <AccordionDetails style={{'padding':'0px'}}>
            {(showPres) && (userType === "Patient")&&<Prescription></Prescription>}
          </AccordionDetails>
        </Accordion>
      }
    </div>
  );
}
