import React , { useState, useEffect}from 'react'
import { Container, FormHelperText} from '@material-ui/core';
import axios from 'axios';
import styles from "../style/profile.module.css";
import profile from '../media/static/profile.png';
import {  TextField, Button, CircularProgress } from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import PublishIcon from '@material-ui/icons/Publish';
import Navtop from '../Landing/Navtop';
import JSONDATA from '../media/static/jsonFiles/statecity.json';
import { useHistory } from 'react-router';
import Bankform from './Bankform';
import { Alert, AlertTitle } from '@material-ui/lab'
import Footer from '../Landing/Footer';
import $ from 'jquery';
import { Link } from "react-router-dom";
import { serverip } from '../Landing/constants'

function Pathoreg(props) {
    const [profilePic, setProfilePic] = useState({ profile });
    const [pathdata, setPathdata] = useState({
        name: '',
        city: '',
        address: '',
        test_price:[]
    })
    const [post, setPost] = useState(false)
    const [username, setUsername] = useState(props.match.params.username)
    const [currUser, setCurrUser] = useState([])
    const [loading, setLoading] = useState(true)
    const [state, setState] = useState('')
    const [testPrice, setTestPrice] = useState({ test: "", price: "" })
    const handleImg = (e) => {
        setProfilePic(e.target.files[0]);
    }
    if (username==="undefined")
        setUsername(localStorage.getItem('name'))
    var history = useHistory();

    const handleInput = (e) => {
        setPathdata({ ...pathdata, [e.target.name]: e.target.value })
    }
    const setTp = (e) => {
        setTestPrice({ ...testPrice, [e.target.name]: e.target.value })
    }

    var form_data = new FormData();
    
    form_data.append('name', pathdata.name);
    form_data.append('state', state);
    form_data.append('city', pathdata.city);
    form_data.append('address', pathdata.address);
    form_data.append('phone', pathdata.phone);
    if (post)
    form_data.append('userpatho', currUser.id);
    useEffect(() => {
         axios.get(`${serverip}/api/detail/Patholab/${username}`,{
            headers: {
              Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(json => {
            setState(json.data.state)
            setPathdata(json.data)
            getuser();
            setLoading(false)
         }).catch(err => {
             const error = (JSON.stringify(err.message))
             if (error.includes("status code 404"))
                 setPost(true)
                 getuser();
              })
    })

    const getuser = async () => {
        await axios.get(`${serverip}/currentuser/`, {
            headers: {
              Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            if (res.data.username === username) {
                setCurrUser(res.data)
                setUsername(res.data.username)
            }
            else history.push('/')
        })     
  }

    const putData = async () => {
        await axios.patch(`${serverip}/api/detail/Patholab/${currUser.username}/`, form_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            $('subBtn').text('Submitted')
            setTimeout(() => {
                $('subBtn').text('Send')
            }, 2000);
        }).catch(error => {
            alert("Please Try again")
        })
    }
   
    const uploadData = async () => {
        if ((pathdata.test_price) !== null) {
            var datajson = JSON.stringify({
                "test_price": [...(pathdata.test_price), testPrice]
            })
        }
         else {
            datajson = JSON.stringify({
                "test_price": [testPrice]
            })
            }
        await axios.patch(`${serverip}/api/detail/Patholab/${currUser.username}/`, datajson, {
            headers: {
                'content-type': 'application/json',
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            $('#tpBtn').text('Uploaded')
            window.location.reload();
        })
    }
    
    const postData = async () => {
        await axios.post(`${serverip}/api/Patholab/`, form_data, {
            headers: {
                'Content-Type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setPost(false)
        }).catch(error => {
            alert("Please Try again....Fill all the details")
        })
    }
    const img_data = new FormData();
    img_data.append('profileImg', profilePic)
    const upload = async () => {
        await axios.patch(`${serverip}/api/detail/Patholab/${currUser.username}/`, img_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            },
           
        }).then($('#imgBtn').text('Uploaded'))
    }
  
    if (!loading)
    return (
        <>
            <Navtop user={currUser.username} user_type="Patholab"></Navtop>
        <div className={styles.main}>
        <Container className={styles.container} maxWidth="lg">
            <div className={styles.top}>
                <div className={styles.imgBox}>
                <img src={((pathdata.profileImg)!==null)?pathdata.profileImg:profile} alt={pathdata.name} />                                               
                </div>
                    <span className={styles.nameplate}><h1>{ pathdata.name}</h1></span>
                </div>
                
            </Container>
            
            <form method="POST" encType="multipart/form-data">
            <div className={styles.grid}>
            <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Lab Name</h2>
                    <input className={styles.userinp} type="text" onChange={handleInput}  placeholder={pathdata.name}  name="name" required />
                        </div>
                    <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Phone No.</h2>
                        <input className={styles.userinp} placeholder={pathdata.phone} type="number" id="phone" name="phone" onChange={handleInput} maxLength="10" required /></div>
                        <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Sign Board Pic</h2>
                            <input type="file" id="fileImg" name="profileImg" accept="image/*" onChange={handleImg} />
                            <Button onClick={upload} variant="contained" id="imgBtn" color="secondary"> Upload </Button>

                      </div>
                    <div className={styles.inpUser}>
                    <h2 className={styles.nameLabel}>State</h2>
                    <Autocomplete
                                freeSolo
                                value={state}
                                placeholder={state}
                                onChange={(event, value) => setState(value)} 
        disableClearable
        options={Object.keys(JSONDATA).map((result) => result)}
        renderInput={(params) => (
            <TextField
            className={styles.userinp}
            {...params}
            variant="outlined"
                name="state"
                size="small"
                style={{ width: '100%', background: "#fff", padding:'1px 2px'}}
                placeholder={pathdata.state}
            // InputProps={{ ...params.InputProps, type: 'search' }}
                                />)} /></div>
            <div className={styles.inpUser}>
          <h2 className={styles.nameLabel}>City</h2>
                            <input className={styles.userinp} placeholder={pathdata.city} type="text" id={styles.city} name="city" onChange={handleInput} required  /></div>
                            <div className={styles.inpUser}>
                  <h2 className="">Address</h2>
                        <textarea name="address" placeholder={pathdata.address} style={{ width: '100%' }} onChange={handleInput} cols="30" rows="3" required></textarea></div>
                            {/* <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>ZIP code</h2>
                        <input className={styles.userinp} type="number" placeholder={pathdata.zip_code} id="zip" onChange={handleInput}  name="zip_code" required /></div> */}
                      
                        
                         
                          <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Currently Open</h2>
                            <input className={styles.userinp} placeholder={pathdata.availability} type="checkbox" id={styles.available} onChange={handleInput} name="availability" />
                            <FormHelperText> This can be changed at Homepage </FormHelperText></div>
                        <Button
                            id="subBtn"
        variant="contained"
        color="primary"
                endIcon={<PublishIcon></PublishIcon>}
                onClick={post? postData: putData }
      >
        Send
      </Button>
                </div>
                
                    </form>
                    
            </div>
            <div className={styles.testlist}>
                <div className={styles.tableData}>
                <table className={styles.table}>
                    <thead>
  <tr >
    <th>Test Name</th>
    <th>Price</th>
                        </tr></thead>
                    <tbody>
                    { ((pathdata.test_price)!==null)?
                        (pathdata.test_price).map((test,index) => {
                            return (
                                <tr key={index}>
                                    <td>{test.test}</td>
                                    <td>{ test.price}</td>
                    </tr>
                            )
                        }):<tr><td>You have not set details yet, Please Set</td></tr>
                        }
                     
                        </tbody>
  
                </table>
                </div>
                <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Test Name</h2>
                    <input className={styles.userinp} type="text" onChange={setTp}   name="test" required />
                    </div>
                        <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Price</h2>
                    <input className={styles.userinp} type="number" onChange={setTp}   name="price" required />
                </div>
                <Button
                    id="tpBtn"
        variant="contained"
        color="primary"
                endIcon={<PublishIcon></PublishIcon>}
                onClick={ uploadData}
      >
        Send
      </Button>
            </div>
            <Bankform user={pathdata.userpatho}></Bankform>
            </>
        )
        else
        return(
          <>
          <Alert onClose={() => {setLoading(false)}} severity="error">
                <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
              </Alert>
          <CircularProgress disableShrink />
            <h1>Authenticating Now...</h1><br/>
            <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
            <Link to='/'>Home</Link>
                <footer>
              <Footer></Footer>
              </footer>
          </>
        )
}

export default Pathoreg
