import { Button } from '@material-ui/core'
import React, { useState } from 'react'
import styles from '../style/prescription.module.css';
import $ from 'jquery'
import axios from 'axios'
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import SaveIcon from '@material-ui/icons/Save';
import { serverip } from '../Landing/constants'

function Presform(props) {
    const [item, setItem] = useState([])
    const [medit, setMedit] = useState([])
    const handleChange = (e) => { 
        setItem({ ...item, [e.target.name]: e.target.value })
   
    }

    
    const addPres = () => {
        $('#apnd').append(`<tr>
        <td>${item.medname}</td>
        <td>${item.dose}</td>
        <td>${item.timing}</td>
        <td>${item.duration}</td>
        
    </tr><span>"+"</td>`)
        var newarray = medit.slice()
        newarray.push(item)
        setMedit(newarray)
        $('#medname').val('')
        $('#dose').val('')
        $('#timing').val('')
        $('#duration').val('')
        
    }
    const submit = async () => {
        if (medit !== null) {
            var datajson = JSON.stringify({
                "medpres": medit,
                "from_user": props.fromuser,
                "for_user": props.foruser,
            })
        }
        else {
            datajson = JSON.stringify({
                "medpres": [medit],
                "from_user": props.fromuser,
                "for_user": props.foruser,
            })
        }
        let conf = window.confirm('Send Prescription')
        if (conf) {
            await axios.post(`${serverip}/api/Prescription/`, datajson, {
                headers: {
                    'content-type': 'application/json',
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
            }).then(res => {
                $('#upBtn').text('Uploaded')
            })
    }
    }

    return (
        <div className={styles.presform} style={{'width':'fit-content','background':'aquamarine'}}>
            <div className={styles.preslist} style={{'width':'inherit'}}>
                <ul className={styles.presul} id="addpres">
                        <table className={styles.table} style={{'width':'minContent', 'fontSize':'0.7rem'}}>
                            <thead>
                                <tr >
                                <th>Name</th>
                                <th>Dose</th>
                                <th>Timing</th>
                                <th>Duration</th>
                                </tr></thead>
                            <tbody id="apnd">
                            </tbody>

                        </table>
                    
                </ul>
            </div>
            <form id="presform" style={{'display':'table-caption'}}>
            <div className={styles.inp}>
                    <label>Med Name</label>
                    <input type="text" name="medname" id="medname" onChange={handleChange} required/>
            </div>
            <div className={styles.inp}>
                    <label>Dose</label>
                    <input type="text" name="dose" id="dose" onChange={handleChange} required/>
            </div>
            <div className={styles.inp}>
                    <label>Timing</label>
                    <input type="text" name="timing" id="timing" onChange={handleChange} required/>
            </div>
            <div className={styles.inp}>
                    <label>Duration</label>
                    <input type="text" name="duration" id="duration" onChange={handleChange} required/>
                </div>
                <div style={{'display':'flex', 'margin':'5px'}}>
                <Button   variant="contained" color="primary" size="small" style={{'marginRight':'2px'}} startIcon={<SaveIcon />} onClick={addPres}>ADD</Button>
                <Button variant="contained" size="small" color="secondary" startIcon={<CloudUploadIcon />} onClick={submit}>Upload</Button></div>
            </form>
           
        </div>
    )
}

export default Presform