
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import styles from '../style/requestlist.module.css'
import $ from 'jquery';
import { Link } from 'react-router-dom';
import { serverip } from './constants'


function Requestlist(props) {
    const [listData, setListData] = useState([])
    const [friend, setFriend] = useState(props.friend)
        
    useEffect(() => {
        const fetchList = async () => {
            await axios.get(`${serverip}/api/request_list/${props.myID}`, {
                headers: {
                          Authorization: `JWT ${localStorage.getItem('token')}`
                        }
            }).then(res => {
                setListData(res.data)
            })
        }
       fetchList();
    }, [])


    const accept = async (e) => {
        var userid = e.target.id
        var data = JSON.stringify({
            "friends": [...friend, userid]
                  })
        axios.all([
          await axios.patch(`${serverip}/api/detail/${props.user_type}/${props.myUser}/`, data, {
                headers: {
                  'Content-Type': 'application/json',
                  Authorization: `JWT ${localStorage.getItem('token')}`
                        },
                        
            }),
           await axios.delete(`${serverip}/api/delete_request/${props.myID}/${userid}`, {
            headers: {
                      Authorization: `JWT ${localStorage.getItem('token')}`
                    }
        })
        ]).then(axios.spread((accepted, deleted) => {
            alert("Accepted")

          }));
    }
    const decline =async (e) => {
        var userid = e.target.id
        let conf = window.confirm('Are you Sure to delete request')
        if (conf) {
            await axios.delete(`${serverip}/api/delete_request/${props.myID}/${userid}`, {
                headers: {
                          Authorization: `JWT ${localStorage.getItem('token')}`
                        }
            }).then(res => {
                $(`#${userid}`).text('Declined')
            })
        }
        else return null
    }
    const Togglelist = () => {
        $('#ul_list').toggleClass(styles.active);
    };
    if(listData.length)
    return (
        <div className={styles.main}>
            <div className={styles.header}><h2 className={styles.heading}>Request List({listData.length})</h2><i style={{'margin':'9px', 'color':'#000'}} className="fa fa-arrow-up" onClick={Togglelist} aria-hidden="true"></i></div>
            <ul id="ul_list">
                {(listData)&&
                    listData.map(frnd => {
                        return (
                            <div className={styles.reqbox} key={frnd.fromusername}>
                    <div className={styles.left}>
                                    <h2 style={{"fontSize": "medium"}}>{ frnd.fromuser}</h2><br />
                                    <Link  to={{
                                        pathname: `userprofile/${frnd.fromusername}`,
                                        state: { id: props.myID }
                                    }} className={styles.profilebtn}>Visit Profile</Link>
                    </div>
                    <div className={styles.right}>
                        <button className={`${styles.btn} accept`} id={frnd.from_user} onClick={accept}>Accept</button>
                        <button className={`${styles.btn} `} style={{"background": "red", 'color':'#fff'}} id={frnd.from_user} onClick={decline}>Decline</button>
                    </div>
                </div>
                        )
                    })
                }
                
            </ul>
        </div>
        )
    else return null
}

export default Requestlist
