import React , { useState, useEffect}from 'react'
import axios from 'axios';
import styles from "../style/profile.module.css";
import profile from '../media/static/profile.png';
import Navtop from './Navtop';
import { Link } from 'react-router-dom';
import Footer from './Footer';
import { Container, Button, CircularProgress } from '@material-ui/core';
import { Alert, AlertTitle } from '@material-ui/lab'
import { serverip } from './constants'

export default function Userprofile(props) {
    const [usertypedata, setUsertypedata] = useState("")
    const [data, setData] = useState([])
    const [tabledta, setTabledta] = useState([])
    const name = props.match.params.username

    useEffect(async () => {
        await axios.get(`${serverip}/api/usertype/${props.match.params.username}`, {
            headers: {
                      Authorization: `JWT ${localStorage.getItem('token')}`
                    }
        }).then((res) => {
            setUsertypedata(res.data)
            axios.get(`${serverip}/api/detail/${res.data.user_type}/${props.match.params.username}`, {
                headers: {
                          Authorization: `JWT ${localStorage.getItem('token')}`
                        }
            })
                .then((userdta) => {
                    setData(userdta.data)
                    if(usertypedata.user_type==="Patholab")
                    setTabledta(userdta.data.test_price)
              }) 
        })
    }, [])


    if(usertypedata.user_type==="Doctor")
    return (
        <>
            <Navtop user={localStorage.getItem('name')} user_type={localStorage.getItem('type')}></Navtop>
        <div className={styles.main}>
        <Container className={styles.container} maxWidth="lg">
            <div className={styles.top}>
                <div className={styles.imgBox}>
                        <img src={((data.profileImg)!==null)?data.profileImg:profile} alt={data.name}></img>
                        
                </div>
                        <span className={styles.nameplate}><h1>{ data.name}</h1></span>
                    </div>
                
            </Container>
            <div className={styles.grid}>
            <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Name</h2>
                        <div className={styles.data}><h3>{ data.name}</h3></div>
                    </div>
                    <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>State</h2>
                            <div className={styles.data}><h3>{ data.state}</h3></div></div>
            <div className={styles.inpUser}>
          <h2 className={styles.nameLabel}>City</h2>
          <div className={styles.data}><h3>{ data.city}</h3></div></div>
                            <div className={styles.inpUser}>
                  <h2 className="">Local Area</h2>
                        <div className={styles.data}><h3>{data.address}</h3></div></div>
                            <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>ZIP code</h2>
                  <div className={styles.data}><h3>{ data.zip_code}</h3></div></div>
                        <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Degree</h2>
                      <div className={styles.data}><h3>{ data.desc}</h3></div></div>
                          <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Specialization Field</h2>
                      <div className={styles.data}><h3>{ data.desc}</h3></div></div>
                          <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Appointment Fee</h2>
                      <div className={styles.data}><h3>&#8377;{ data.apnt_fees} </h3></div></div>
                          <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Consult Fee</h2>
                      <div className={styles.data}><h3>&#8377; { data.consult_fees} </h3></div></div>
                          <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Clinic Address</h2>
                      <div className={styles.data}><h3>{ data.clinic_add}</h3></div></div>
                          <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Availability</h2>
                        <div className={styles.data}><h3>{(data.availability)? "Present - " : "Absent -  " }</h3></div></div>
                </div>
            </div>
            <Footer></Footer>
            </>
        )
    else if(usertypedata.user_type === "Patient")
        return (
            <>
                  <Navtop user={localStorage.getItem('name')} user_type={localStorage.getItem('type')}></Navtop>
        <div className={styles.main}>
        <Container className={styles.container} maxWidth="lg">
            <div className={styles.top}>
                <div className={styles.imgBox}>
                        <img src={((data.profileImg)!==null)?data.profileImg:profile} alt={data.name}></img>
                        
                </div>
                            <span className={styles.nameplate}><h1>{ data.name}</h1></span>
                </div>
                
            </Container>
            <div className={styles.grid}>
            <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Name</h2>
                            <div className={styles.data}><h3>{data.name}</h3></div>
                    </div>                   
                    <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>State</h2>
                            <div className={styles.data}><h3>{ data.state}</h3></div></div>
            <div className={styles.inpUser}>
          <h2 className={styles.nameLabel}>City</h2>
                            <div className={styles.data}><h3>{ data.city}</h3></div></div>
                            <div className={styles.inpUser}>
                  <h2 className="">Local Area</h2>
                            <div className={styles.data}><h3>{ data.address}</h3></div></div>
                            <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>ZIP code</h2>
                            <div className={styles.data}><h3>{ data.zip_code}</h3></div></div>
                          
                </div>
                    
                </div>
                <Footer></Footer>
            </>
        )
    else  if(usertypedata.user_type === "Hospital")
        return (
            <>
             <Navtop user={localStorage.getItem('name')} user_type={localStorage.getItem('type')}></Navtop>
        <div className={styles.main}>
        <Container className={styles.container} maxWidth="lg">
            <div className={styles.top}>
                <div className={styles.imgBox}>
                        <img src={((data.profileImg)!==null)?data.profileImg:profile} alt={data.name}></img>
                        
                </div>
                            <span className={styles.nameplate}><h1>{ data.name}</h1></span>
                </div>
                
            </Container>
            <div className={styles.grid}>
            <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Name</h2>
                            <div className={styles.data}><h3>{data.name }</h3></div>
                        </div>
                        <div className={styles.inpUser}>
          <h2 className={styles.nameLabel}>Total Beds</h2>
                            <div className={styles.data}><h3>{ data.total_bes}</h3></div></div>
                        <div className={styles.inpUser}>
          <h2 className={styles.nameLabel}>Available Beds</h2>
                            <div className={styles.data}><h3>{ data.available_bed}</h3></div></div>
                    <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Phone No.</h2>
                            <div className={styles.data}><h3>{data.phone }</h3></div>
                        </div>
                   
                    <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>State</h2>
                            <div className={styles.data}><h3>{ data.state}</h3></div></div>
            <div className={styles.inpUser}>
          <h2 className={styles.nameLabel}>City</h2>
                            <div className={styles.data}><h3>{ data.city}</h3></div></div>
            
                            <div className={styles.inpUser}>
                  <h2 className="">Local Area</h2>
                            <div className={styles.data}><h3>{ data.address}</h3></div></div>
                          <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Booking Charge</h2>
                            <div className={styles.data}><h3>{ data.booking_price}</h3></div></div>
                          
                </div>
                    
                </div>
                <Footer></Footer>
            </>
        )
        else  if(usertypedata.user_type === "Ambulance")
        return (
            <>
                  <Navtop user={localStorage.getItem('name')} user_type={localStorage.getItem('type')}></Navtop>
        <div className={styles.main}>
        <Container className={styles.container} maxWidth="lg">
            <div className={styles.top}>
                <div className={styles.imgBox}>
                        <img src={((data.profileImg)!==null)?data.profileImg:profile} alt={data.name}></img>
                        
                </div>
                            <span className={styles.nameplate}><h1>{data.name }</h1></span>
                </div>
                
            </Container>
            <div className={styles.grid}>
            <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Name</h2>
                            <div className={styles.data}><h3>{data.name}</h3></div>
                    </div>                   
                    <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>State</h2>
                            <div className={styles.data}><h3>{ data.state}</h3></div></div>
            <div className={styles.inpUser}>
          <h2 className={styles.nameLabel}>City</h2>
                            <div className={styles.data}><h3>{ data.city}</h3></div></div>
                            <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>Service Area</h2>
                            <div className={styles.data}><h3>{ data.address}</h3></div></div>
                            <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>ZIP code</h2>
                            <div className={styles.data}><h3>{ data.zip_code}</h3></div></div>
                           
                            <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>With In City Charge</h2>
                            <div className={styles.data}><h3> &#8377;{ data.wic_charge}</h3></div></div>
                            <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>Vehicle Registration</h2>
                            <div className={styles.data}><h3> &#8377;{ data.vehicle_reg}</h3></div></div>
                            <div className={styles.inpUser}>
                  <h2 className={styles.nameLabel}>Ambulance Owner Name</h2>
                            <div className={styles.data}><h3>{data.owner_name}</h3></div></div>
                            <div className={styles.inpUser}>
                      <h2 className={styles.nameLabel}>Availability</h2>
                        <div className={styles.data}><h3>{(data.availability)? "Present - " : "Absent -  " }</h3></div></div>
                          
                </div>
                    
                </div>
                <Footer></Footer>
            </>
        )
    else  if(usertypedata.user_type === "Patholab")
      return (
            <>
                <Navtop user={localStorage.getItem('name')} user_type={localStorage.getItem('type')}></Navtop>
                <div className={styles.main}>
                    <Container className={styles.container} maxWidth="lg">
                        <div className={styles.top}>
                            <div className={styles.imgBox}>
                                <img src={((data.profileImg)!==null)?data.profileImg:profile} alt={data.name} alt="profile"></img>
                        
                            </div>
                          <span className={styles.nameplate}><h1>{data.name}</h1></span>
                        </div>
                
                    </Container>
                    <div className={styles.grid}>
                        <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>Name</h2>
                            <div className={styles.data}><h3>{data.name}</h3></div>
                        </div>
                        <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>State</h2>
                            <div className={styles.data}><h3>{data.state}</h3></div></div>
                        <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>City</h2>
                            <div className={styles.data}><h3>{data.city}</h3></div></div>
                        <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>Lab Address</h2>
                            <div className={styles.data}><h3>{data.address}</h3></div></div>
    
                        {/* <div className={styles.inpUser}>
                            <h2 className={styles.nameLabel}>Availability</h2>
                            <div className={styles.data}><h3>{(data.availability) ? "Present - " : "Absent -  "}</h3></div></div> */}
                          
                    </div>
                    <div style={{'width':'100%'}}>
                        <h1 style={{ 'letterSpacing': '1px','color':'#fff', 'margin':'auto', 'textAlign':'centre' }}>Test and Price</h1>
            
                        <div className={styles.preslist}>
                          <table style={{ "font-size":"12px", "width": '100%', "text-align": "center", "overflowY":'auto'}} className={styles.table}>
                                    <thead>
                   
                                    <tr >
                                       
                                        <th>Test</th>
                                        <th>Price</th>
                    
                                    </tr></thead>
                                <tbody>
                                    {(tabledta.length)?(tabledta).map((tp, index) => {
                                        return (
                                            
                                            <tr>
                                                
                                                <td>{tp.test}</td>
                                                <td>&#8377;{tp.price}</td>
                                            </tr>
                                        )
                                    }):null
                                    }
                                </tbody>
  
                            </table>
                        </div>
                    </div>
                   
              </div>
              <Footer></Footer>
              
            </>
        )
        else
        return(
          <>
          <Alert severity="error">
                <AlertTitle>!!! Inauthenticated User !!!</AlertTitle>
              </Alert>
          <CircularProgress disableShrink />
            <h1>Create an account</h1><br/>
            <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
            <Link  to='/'>Go Back</Link>
            <footer>
              <Footer></Footer>
              </footer>
          </>
        )
}
