import React from 'react'
import jsPDF from 'jspdf'
import styles from '../style/appointment.module.css'
import { Button } from '@material-ui/core';


export default function Pdf(props) {
    const download = () => {
        var doc = new jsPDF('p', 'pt', 'a4');
        var pdf = doc.html(document.querySelector('#container'))
            pdf.save("mypdf.pdf");        
    }

    
    return (
        <>
        <div  id="container" style={{"textAlign":'center', 'width':'562px'}}>
            <h2 style={{'textAlign':'center'}}>Receipt</h2>
            <div className={styles.contpdf}>
            <div className={styles.left} >
                        <div className={styles.inp} ><label htmlFor="">Name :</label><span>{ props.mydata.name}</span> </div><br/>
                        <div className={styles.inp} ><label htmlFor="">city :</label><span> { props.mydata.city}</span>  </div><br/>
                       <div className={styles.inp} ><label htmlFor="">State :</label><span> { props.mydata.state}</span> </div><br/>
                    <div className={styles.inp} ><label htmlFor="">Phone: </label><span>{ props.mydata.phone}</span> </div><br/>
                    <div className={styles.inp} ><label htmlFor="">Patient Name: </label><span>{ props.ptname}</span> </div><br/>
                       <div className={styles.inp} ><label htmlFor="">Aadhar Number : </label><span>{ props.aadhar}</span> </div><br/>
                        <div className={styles.inp}  ><label htmlFor="">Address :</label><span>{ props.mydata.address}</span> </div><br />
                        <div className={styles.inp}  ><label htmlFor="">Transaction Id :</label><span>{props.transid }</span> </div><br />
                        
          </div>
            <div className={styles.right} >
                        <div className={styles.inp} ><label htmlFor="">Name :</label><span>{ props.rcvdata.name}</span> </div><br/>
                        <div className={styles.inp} ><label htmlFor="">city :</label><span>{ props.rcvdata.city}</span>  </div><br/>
                       <div className={styles.inp} ><label htmlFor="">State :</label><span> { props.rcvdata.state}</span> </div><br/>
                        <div className={styles.inp} ><label htmlFor="">Phone: </label><span>{props.rcvdata.phone}</span> </div><br />
                        {(props.collector !== undefined) && (props.collector) &&
                    (<div className={styles.inp} ><label htmlFor="">Patient Name: </label><span>{ props.collector}</span> </div>)
                        }
                        <div className={styles.inp} ><label htmlFor="">Aadhar Number : </label><span>{ props.puid}</span> </div><br/>
                        <div className={styles.inp}  ><label htmlFor="">Amount</label><span> &#8377; { props.amount}</span> </div><br />
                        <div className={styles.inp}  ><label htmlFor="">Generated Id :</label><span>{ props.generatedid}</span> </div><br />
                        
                </div>
             
            </div>
            <div className={styles.ulcont}>
            <ul>
                    <li>This is a computer generated receipt</li>
                    <li>Authentication shall be done during appointment</li>
                    <li>This does not guarantee the appointment</li>
                    <li>Transaction shall be considered null and void if receipt does not have a valid transaction ID</li>
                    <li>Patient must carry receipt during appointment</li>
                    <li>Patient must carry Aadhar Card with number provided during transaction</li>
                    <li>If Aadhar Number does not match with the one in the receipt, Transaction might get cancelled depending upon the payment receiver's choice</li>
                    <li>Appointment shall be cancelled if Patient could not reach on time as mentioned in the receipt, depending upon payment receiver's choice</li>
                    <li>100 % amount shall be refunded in any case of failure of appointment except scheduled timing failure </li>
                    <li>25 % amount shall be refunded in case Patient could not reach on time, and appointment is cancelled by the payment receiver </li>
                    <li>Refund shall be initiated instantly after appointment cancellation by payment receiver </li>
                    <li>Refund shall not be initiated in any case of illegal action or activity</li>
                    <li>Payment receiver shall be solely responsible for payment refund and transaction authentication</li>
                </ul>
                </div>
        </div>
        <Button variant="contained" color="secondary" onClick={download}> Download Receipt</Button>
        </>
    )
}
