import { Button, CircularProgress} from '@material-ui/core'
import React, {useState, useEffect} from 'react'
import online from '../media/static/online.png';
import styles from '../style/chat.module.css';
import DeleteIcon from '@material-ui/icons/Delete';
import { Avatar } from '@material-ui/core';
import $ from 'jquery'
import {  client, w3cwebsocket as W3CWebSocket } from 'websocket';
import axios from 'axios';
import SendIcon from '@material-ui/icons/Send';
import { useHistory } from "react-router";
import Presform from '../info/Presform'
import Navtop from '../Landing/Navtop';
import { Link } from "react-router-dom";
import { Alert, AlertTitle } from '@material-ui/lab';
import Footer from '../Landing/Footer';
import {serverip} from '../Landing/constants'

function Chat(props) {
    const [username, setUsername] = useState("")
    const [msgInput, setMsgInput] = useState("")
    const [messages, setMessages] = useState({})
    const [userinfo, setUserinfo] = useState([])
    const [chats, setChats] = useState([])
    const [inbox, setInbox] = useState([])
    const [chaterror, setChaterror] = useState(false)
    const [image, setImage] = useState()
    const [mdelalert, setMdelalert] = useState(false)
    const [writealert, setWritealert] = useState(false)
    const [chatAlert, setChatAlert] = useState(false)
    const [deleteinfo, setDeleteinfo] = useState(false)
    const [tick, setTick] = useState(false)
    const [roomname, setRoomname] = useState(props.match.params.id)
    const [listuser, setListuser] = useState([])
    const [canchat, setCanchat] = useState(false)
    const [time, setTime] = useState("")
    const [loading, setLoading] = useState(true)
    const [usertype, setUsertype] = useState("Patient")
    var foruser=''
    var fromuser=''
    var formData = $('#form')
    var id = 0;
    useEffect(() => {
        const fetchData = async () => {
            axios.get(`${serverip}/currentuser/`, {
                headers: {
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
            }).then((requests) => {
                id = requests.data.id
                setUserinfo(requests.data)
                axios.get(`${serverip}/chat/threadlist/${requests.data.id}`, {
                    headers: {
                        Authorization: `JWT ${localStorage.getItem('token')}`
                    }
                }).then(req => {
                    setLoading(false)
                    setInbox(req.data)
                    checkuser()
                    getUserChats()
                    
                })
            })
        }
        fetchData();
    }, [roomname])
    const getUserChats =() => {
        axios.get(`${serverip}/chat/chatlist30/${roomname}/`, {
            headers: {
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then((response) => {
            setChats(response.data)
            $('#msgbar ').animate({scrollTop: $('#msgbar')[0].scrollHeight}, 'slow');
        })
    }

    const checkuser = () => {
        if (roomname) {
            axios.get(`${serverip}/api/detail/Doctor/${roomname}`, {
                headers: {
                    Authorization: `JWT ${localStorage.getItem('token')}`
                }
            }).then(respo => {
                if (respo.data.friends) {
                    setUsertype("Doctor")
                    respo.data.friends.forEach(element => {
                        if (element === id) {
                            getUserChats()
                            return null
                        }
                        else {
                            setCanchat(false)
                            setChatAlert(true)
                            setTimeout(() => {
                                history.push('/inbox')
                            }, 2000);
                        }
                    })
                }
                else {
                    getUserChats()
                    return null
                }
            })
        }
    }
   
    const handletext = (e) => {
            setMsgInput(e.target.value)
    }
    const togglePrep = () => {
        $('.' + styles.prepbar).toggleClass(styles.toggleprep);
    }
    const showimg = () => {
        $('.' + styles.fileinp).toggleClass(styles.activeimg);
    }
    const handleImg = (e) => {
        setImage(e.target.files[0]);
    }
    const upload = async () => {
        const img_data = new FormData();
        img_data.append('recuser', roomname)
        img_data.append('user', chats[0].user)
        img_data.append('thread', chats[0].thread)
        img_data.append('image', image)
        await axios.post(`${serverip}/chat/chatimg/`, img_data, {
            headers: {
                'content-type': 'multipart/form-data',
                Authorization: `JWT ${localStorage.getItem('token')}`
            },
           
        }).then(res => {
            $('#upld').text('Uploaded')
            getUserChats()
            setTimeout(() => {
                $('#upld').text('Upload')
            }, 2000);
        }).catch("Try sending image after a message")
    }
    
        
    const client = new W3CWebSocket(`ws://127.0.0.1:8000/chat/messages/${roomname}/?token=${localStorage.getItem('token')}`)
    useEffect(() => {
        client.onopen = (e) => {
            formData.submit(function (event) {
                event.preventDefault();
                var msgText = msgInput
                const finalData = {
                    'message': msgInput
                }
                client.send(JSON.stringify(finalData))
            })
        }
        client.onmessage = (message) => {
            const chatDataMsg = message.data
            if (chatDataMsg) {
                setMessages({
                    ...messages,
                    msg: chatDataMsg
                })
                getUserChats()
            }
        }
        
        client.onerror = function (e) {
            setChaterror(true)
        }
        client.onclose = function (e) {
            
            
        }
    }, [])
    const sendmessage = (e) => {
        if (msgInput !== "") {
            client.send(JSON.stringify({
                type: "message",
                message: msgInput,
                time: time
            }));
            setTick(true)
            getUserChats();
            setMsgInput("")
        }
        else {
            setWritealert(true)
            setTimeout(() => {
                setWritealert(false)
            }, 2000);
        }
    }
   
    let history = useHistory();

    const delete_user = async (e) => {
        e.preventDefault()
        var conf = window.confirm("Are You sure to delete this user")
        if (conf){
        var deluser = e.target.id
        axios.get(`${serverip}/api/detail/Doctor/${userinfo.username}`, {
            headers: {
                Authorization: `JWT ${localStorage.getItem('token')}`
            }
        }).then(async (res) => {
            setListuser(res.data.friends)
            var newlist = []
            res.data.friends.forEach(element => {
                    if (element !== parseInt(deluser))
                    newlist.push(element)
            });
            var data = (JSON.stringify({
                "friends": newlist
            }))
            await axios.patch(`${serverip}/api/detail/Doctor/${userinfo.username}/`, data, {
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `JWT ${localStorage.getItem('token')}`
                },
                        
            }).then(alert("Deleted"))
            
        }).catch(err => {
            var error404 = JSON.stringify(err)
            if (error404.includes("status code 404"))
            alert("Only Doctor can remove user")
        })
    }
    }
    const deletechat = async (e) => {
        var delthreadid=e.target.id
        var confirm = window.confirm("Do you want to Delete this Chat")
        if (confirm) {
            await axios.delete(`${serverip}/chat/threaddetail/${delthreadid}`,{
                headers: {
                    Authorization: `JWT ${localStorage.getItem('token')}`
                },
                        
            }).then(res => {
                setCanchat(false)
            }).catch(err => {
                alert("Some error occurred")
            })
        }
    }
    const deletemessage = async (thread, msgid) => {
        var confirm = window.confirm("Do you want to Delete this message")
        if (confirm) {
            await axios.delete(`${serverip}/chat/deletechat/${thread}/${msgid}`,{
                headers: {
                    Authorization: `JWT ${localStorage.getItem('token')}`
                },
                        
            }).then(res => {
                setMdelalert(true)
                getUserChats()
                setTimeout(() => {
                    setMdelalert(false)
                }, 2000);
            }).catch(err => {
                alert("Some error occurred")
            })
        }
        else return null
    }
    const ToggleClass1 = () => {
        $('.' + styles.chat_container).toggleClass(`${styles.activeChat}` );
        $('.' + styles.search_container).toggleClass(`${styles.activeChat}`);
}
if(!loading)   
    return (
        <>
            {chatAlert ?
        (<Alert  onClose={() => {setChatAlert(false)}} severity="error">
          <AlertTitle>Not in your Consult list </AlertTitle>
       <strong>Please Send request</strong>
                </Alert>) : null}
            {deleteinfo ?
        (<Alert  onClose={() => {setDeleteinfo(false)}} severity="success">
          <AlertTitle>Deleted </AlertTitle>
                </Alert>) : null}
            
    
            <Navtop user={userinfo.username} user_type={usertype}></Navtop>
        <div className={ styles.chat_container} id="chat_container">

                <div className={styles.search_container}>
                    <div onClick={ToggleClass1}  style={{'margin':'3px 22px 5px 19px', 'width':'10px', 'cursor':'pointer'}}>
                <i className="fa fa-ellipsis-v" style={{"width": "30px" , "height": "30px",  "textAlign": 'center'}}  aria-hidden="true"></i></div>
                <input type="text" placeholder="Search" />

                </div>
            <div className={`${styles.conversation_list} ${styles.active}`}>
            
                {(inbox.length)?inbox.map(thread => {
                        var str = thread.timestamp; var date = str.slice(0, 10); var time = str.slice(11,19)
                    if (thread.first === userinfo.id) { var fullname = thread.second_fullname; var userN = thread.seconduser; var other = thread.second; foruser = thread.second; fromuser=thread.first;}
                    else if (thread.second === userinfo.id) { fullname = thread.first_fullname; userN = thread.firstuser; other = thread.first; fromuser = thread.second; foruser=thread.first; }
                        
                            return (
                                <p href={`/consult/${userN}`} key={thread.id} style={{ 'listStyle': 'none', 'textDecoration': 'none' }} >
                                    <div className={styles.conversation} >
                                <div className={styles.title_text}><a style={{'color':'#fff'}} href={`/consult/${userN}`}>{fullname} </a>
                            <Link to={`/userprofile/${userN}`} style={{'float':'right', 'fontSize':'8px', 'marginLeft':'2px'}}  className="btn btn-success">Visit Profile</Link><button style={{'float':'right', 'fontSize':'8px'}} onClick={delete_user} id={other} className="btn btn-danger">Remove user</button><button style={{'float':'right', 'fontSize':'8px'}} onClick={deletechat} id={thread.id} className="btn btn-danger">Delete Chat</button>
                        </div>
                        <div className={styles.created_date}>
                            {date} {time}
                        </div>
                        <div className={styles.conversation_message}>
                           
                        </div>
                        </div>
                        </p>
                            )}):<><CircularProgress></CircularProgress><p>wait for a while or No one is in your list...Send or accept request</p></>
                    }
                    
                <div id="prepbar" className={styles.prepbar} >
                    <Presform foruser={foruser} fromuser={fromuser}></Presform>
                    </div>
                    
                </div>
                <div className={styles.new_message_container}>
                    <div>
                    <button className={styles.btn} id="presbar" onClick={togglePrep}>Prescription</button>
                   <button style={{'margin':'auto', 'height':'fit-content'}} onClick={showimg}>Image</button></div>
                    <div className={styles.fileinp}  id="fileImg">
                    <input className={styles.imginp} type="file" name="image" accept="image/*" onChange={handleImg} /><br />
                <Button id="upld" className={styles.upbtn} variant="contained" color="primary"  endIcon={<SendIcon />} onClick={upload}>Upload</Button></div>
                </div>
                <div className={styles.chat_title}>
                {mdelalert ? (<Alert severity="success"> <AlertTitle>Deleted </AlertTitle></Alert>) : null}
                {writealert ? (<Alert severity="error"> <AlertTitle>Write Something </AlertTitle></Alert>) : null}
                <div style={{'display': 'inline-flex','marginTop': '17px'}}>
                    <span className={styles.title}>{ roomname}</span>
                <span><img src={ online} height="8px" width="10px" alt="Online"></img></span></div>
                <Avatar onClick={deletechat}  id={(chats[0])?chats[0].thread:null} style={{'background':'pink', 'color':'#fff', 'float':'right', 'margin':'10px', 'cursor':'pointer'}}>
                    <DeleteIcon className={styles.delete_icon} ></DeleteIcon>
                    </Avatar>
                    
                </div>
                
                <div className={styles.chat_message_list} id="msgbar">
                    {(chats.length)?
                        chats.map(chat => {
                    var str = chat.timestamp; var date = str.slice(0, 10); var time = str.slice(11,19)
                    if (chat.user === userinfo.id) {
                        
                        return (
                            <div key={chat.id} className={`${styles.message_row} ${styles.sender}`}>
                                
                            <div className={styles.message_content} id="chatSender">
                                    <div className={styles.msg_text} >
                                        {chat.message ? chat.message : chat.image ? <img src={chat.image}/>:null}
                                        {((chat.image)!==null)?<img  loading="lazy" className={styles.msgimg} src={chat.image} alt="image"/>:null}
                                </div><i style={{'color':'green', 'fontSize':'6px'}} className="fa fa-check" aria-hidden="true"></i>
                                <div className={styles.msg_time}>{date} { time }<i className="fa fa-trash-o" style={{'color':'red', 'cursor':'pointer'}} id={chat.id} onClick={()=>deletemessage(chat.thread, chat.id)} aria-hidden="true"></i></div>
                            </div>
                        </div>)
                    }
                    
                    else {
                        return (
                            <div className={`${styles.message_row} ${styles.receiver}`}>
                                <div className={styles.message_content} id="chatReceiver">
                                    <div className={styles.msg_text}>{chat.message}
                                    {((chat.image)!==null)?<img  loading="lazy" className={styles.msgimg} src={chat.image} alt="image"/>:null}</div>
                                    <div className={styles.msg_time}>{date} { time }<i className="fa fa-trash-o" style={{'color':'red', 'cursor':'pointer'}} id={chat.id} onClick={()=>deletemessage(chat.thread, chat.id)} aria-hidden="true"></i></div>
                                </div>
                            </div>
                        )
                    }
                }):<CircularProgress></CircularProgress>}                
                </div>
            <form className={styles.chat_form} id="form" noValidate onSubmit={sendmessage}>
                <input type="text" placeholder="Type Your Message" value={msgInput} onChange={handletext}/>
                    <Button variant="contained" color="primary" style={{"height":" min-content","margin":" auto"}} endIcon={<SendIcon />} onClick={sendmessage}></Button>
                </form>
        </div>
        </>
        )
        else
        return(
          <>
          <Alert onClose={() => {setLoading(false)}} severity="error">
                <AlertTitle>!!! Please Wait...Loading !!!</AlertTitle>
              </Alert>
          <CircularProgress disableShrink />
            <h1>Please Wait...or reload</h1><br/>
            <Button variant="contained" color="primary" onClick={()=> window.location.reload()}>Reload</Button><br />
            <Link  to='/'>Home</Link>
            <footer>
              <Footer></Footer>
              </footer>
          </>
        )
}

export default Chat
