import React, { useState }from 'react'
import styles from "../style/login.module.css";
import { Link, useHistory } from "react-router-dom";
import logo from "../media/static/Logomain.JPG";
import fb from "../media/static/facebook.png";
import gmail from "../media/static/gmail.png";
import twitter from "../media/static/twitter.png";
import { Alert, AlertTitle } from '@material-ui/lab'
import Footer from '../Landing/Footer'
import { serverip } from '../Landing/constants'


function Login(props) {
  const [cred, setCred] = useState({
    user: "",
    pass: "",
  })
  const [error, setError] = useState(false)
  const handleInp = (e) => {
    setCred({ ...cred, [e.target.name]: e.target.value })
  }

  var history = useHistory()
  const handle_login = e => {
    e.preventDefault()
    fetch(`${serverip}/api/token-auth/`, {
      method: 'POST',
      body: JSON.stringify({
        username: cred.user,
        password: cred.pass
      }),
      headers: { 'Content-Type': 'application/json' },
    })
      .then(res => res.json())
      .then(json => {
        if (json.token) {
          localStorage.setItem('token', json.token);
          localStorage.setItem('name', cred.user)
          history.push('/home')
        }
        else setError(true)
      })
      .catch(function (error) {
        alert("Please Try after Sometime")
        
      })
  }
  
        return (
          <React.Fragment>
            {error ? 
          (<Alert onClose={() => {setError(false)}} severity="error">
            <AlertTitle>Wrong Credential</AlertTitle>
        Check Your username or Password <strong>check it out!</strong>
          </Alert>):null}
            <section className={styles.section}>
                <div className={styles.imgBx}>
                    <img src={logo} alt="Welcome to DocPlus" style={{'height':'548px'}} />
                </div>
                <div className={styles.contentBx}>
                    <div className={styles.formBx}>
                        <h2>Login</h2>
                  <form method="POST" onSubmit={handle_login}>
                            <div className={styles.inputBx}>
                                <label htmlFor="id_username">Username:</label>
                                <span>
                                    <input type="text" name="user" onChange={handleInp} placeholder="Enter your username or Email" maxLength="50" required id="id_username" />
                                </span>
                            </div>
                            <div className={styles.inputBx}>
                                <label htmlFor="id_password">password:</label>
                                <span>
                                    <input
                                        type="password"
                                        name="pass"
                                            className={styles.inpBx}
                                            onChange={handleInp}
                                        placeholder="Enter your password"
                                        maxLength="50"
                                        required
                          id="id_password"
                                    />
                                </span>
                            </div>
                            <div className={styles.inputBx}>
                                <input type="submit"  value="Sign in" name="" />
                            </div>
                                <div className={styles.inputBx}>
                                <p>
                                    Don't have an account?<Link exact to="/Signup">Sign Up</Link>
                                </p>
                            </div>
                        </form>
                        <h3>Login with Social media</h3>
                        <ul className={styles.sci}>
                            <li>
                                <img src={fb} width="30px" height="30px" alt="Facebook" />
                            </li>
                            <li>
                                <img src={gmail} width="30px" height="30px" alt="Google" />
                            </li>
                            <li>
                                <img src={twitter} width="30px" height="30px" alt="twitter" />
                            </li>
                        </ul>
                    </div>
                </div>
            </section>
            
            
            <footer>
            <Footer></Footer>
          </footer>
        </React.Fragment>
        )};
export default Login;

