
import React from 'react'
import styles from '../style/clock.module.css';
import $ from 'jquery';

function Clock() {
    setInterval(() => {
        const deg = 6
        const day = new Date();
        const hh = day.getHours() * 30;
        const mm = day.getMinutes() * deg;
        const ss = day.getSeconds() * deg;
        $('#hr').css('transform', `rotatez(${(hh) + (mm / 12)}deg)`);
    $('#mn').css('transform', `rotateZ(${mm}deg)`);
    $('#sc').css('transform', `rotateZ(${ss}deg)`);
    }, []);
    
    return (
        <div className={styles.body}>
            <div className={styles.clock}>
                <h1>AKJ</h1>
            <div className={styles.hour}>
                <div className={styles.hr} id="hr"></div>
            </div>
            <div className={styles.min}>
                <div className={styles.mn} id="mn"></div>
            </div>
            <div className={styles.sec}>
                <div className={styles.sc} id="sc"></div>
            </div>
            </div>
            </div>
    )
}

export default Clock
