
import React from 'react'
import { Link, Redirect, useHistory } from 'react-router-dom';
import styles from '../style/navtop.module.css';
import $ from 'jquery';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import logo from '../media/static/logo512.png'

function Navtop(props) {
    $(function(){
        $(window).scroll(function () {
            $('.' + styles.header).toggleClass(styles.sticky, window.scrollY>0);
        });
    });
    const handle_logout = () => {
        localStorage.removeItem('token');
        localStorage.removeItem('name');
        localStorage.removeItem('type');
    };
    const remNav = () => {
        $('#header').toggleClass(styles.toggleNav);
    }
    return (
        <>
        <KeyboardArrowDownIcon onClick={remNav} className={styles.toggleBtn}></KeyboardArrowDownIcon>
            <header className={styles.header} id="header">
                <div style={{'display':'flex', whiteSpace:'nowrap'}}>
            <img src={logo} height='50px' width='50px'></img>
                    <Link to="#" className={styles.logo}>HOMO SAVIOUR</Link>
                    </div>
            <ul>
                    <li>{props.user}</li>
                <li><Link className={styles.link} to="/home">Home</Link></li>
                <li><Link  className={styles.link} to={`/profile/${props.user_type}/${props.user}`}>Profile</Link></li>
                <li><Link className={styles.link} to="/forumarea">Forum</Link></li>
                <li><Link className={styles.link} to="/inbox">Inbox</Link></li>
                <li><Link className={styles.link}  onClick={handle_logout} to='/Login'>Log Out</Link></li>
            </ul>
            
            </header>
            </>
    )
}

export default Navtop
